import logo from "../../assets/images/logo.jpg";
import NavCarousel from "./NavCarousel";
import {Disclosure} from "@headlessui/react";
import {Bars3Icon, XMarkIcon} from "@heroicons/react/24/outline";
import NavAccordion from "./NavAccordion";
import MobileUserNav from "./MobileUserNav";
import DesktopUserNav from "./DesktopUserNav";
import React from "react";
import {AppInfo, RequiredPermission, UserSummary} from "../../SessionManager";

export interface PageProps {
    refreshApp: (appInfo: AppInfo) => void
    updateMainContent: (page: Page) => void
}

export interface Page {
    pageName: string
    pageDescription: string
    pageIcon: React.ForwardRefExoticComponent<Omit<React.SVGProps<SVGSVGElement>, "ref"> & {title?: string | undefined, titleId?: string | undefined} & React.RefAttributes<SVGSVGElement>>
    pageContent: (props: PageProps) => JSX.Element
    requiredAccessRights: RequiredPermission[]
}

export interface PageGroup {
    groupName: string
    pages: Page[]
}

export interface NavBarInfo {
    isGuestSession: boolean
    userSummary: UserSummary
    userNavigation: PageGroup[]
    updateMainContent: (page: Page) => void
    refreshApp: (appInfo: AppInfo) => void
}

export default function NavBar({isGuestSession, userSummary, userNavigation, updateMainContent, refreshApp}: NavBarInfo) {
    return (
        <Disclosure as="nav" className="border-b border-gray-700">
            {({open}) => (
                <>
                    <div className="w-screen px-4 sm:px-6 lg:px-8 ">
                        <div className="h-16 flex justify-between">
                            <div className={"w-10 flex items-center justify-center"}>
                                <img
                                    className="h-8 w-8"
                                    src={logo}
                                    alt="AUN Cafeteria Logo"
                                />
                            </div>

                            <div className={"hidden md:inline-flex w-full flex-row"}>
                                <NavCarousel
                                    userNavigation={userNavigation}
                                    updateMainContent={updateMainContent}
                                />
                            </div>

                            <div className={"hidden md:inline-flex items-center justify-center w-10"}>
                                {/* Profile dropdown */}
                                <DesktopUserNav
                                    isGuestSession={isGuestSession}
                                    userSummary={userSummary}
                                    refreshApp={refreshApp}
                                    updateMainContent={updateMainContent}
                                />
                            </div>

                            <div className={"flex items-center md:hidden"}>
                                {/* Mobile menu button */}
                                <Disclosure.Button
                                    className="inline-flex items-center justify-center rounded-md bg-gray-800 p-2 hover:bg-gray-700 hover:text-gray-100">
                                    <span className="sr-only">Open main menu</span>
                                    {open ? (
                                        <XMarkIcon className="block h-6 w-6" aria-hidden="true"/>
                                    ) : (
                                        <Bars3Icon className="block h-6 w-6" aria-hidden="true"/>
                                    )}
                                </Disclosure.Button>

                            </div>
                        </div>
                    </div>

                    <Disclosure.Panel className="md:hidden">
                        <>
                            <NavAccordion userNavigation={userNavigation} updateMainContent={updateMainContent}/>
                            <MobileUserNav
                                isGuestSession={isGuestSession}
                                userSummary={userSummary}
                                updateMainContent={updateMainContent}
                                refreshApp={refreshApp}
                            />
                        </>
                    </Disclosure.Panel>
                </>
            )}
        </Disclosure>
    )
}