import {useState} from "react";
import NavPopover from "./NavPopover";
import {ChevronLeftIcon, ChevronRightIcon} from "@heroicons/react/24/outline";
import {resolveClassNames} from "../../Helpers/ApplicationHelpers";
import {Page, PageGroup} from "./NavBar";


export default function NavCarousel({userNavigation, updateMainContent}: {userNavigation: PageGroup[], updateMainContent: (page: Page) => void }) {

    const navigationLength = userNavigation.length

    const [startIndex, setStartIndex] = useState(0);
    let endIndex = startIndex + 5;

    const leftToggleable = (navigationLength > 6) && (startIndex > 0);
    const rightToggleable = (navigationLength > 6) && (endIndex < (navigationLength - 1));

    let popovers = [];

    if (navigationLength <= 6) {
        popovers = userNavigation.map((pageGroup, key) => (
            <div className={"h-full flex items-center w-1/6 px-1"} key={key}>
                <NavPopover
                    groupName={pageGroup.groupName}
                    pages={pageGroup.pages}
                    updateMainContent={updateMainContent}
                />
            </div>
        ))
    } else {
        for (let i = startIndex; i <= endIndex; i++) {
            let navItem: PageGroup = userNavigation[i];
            popovers.push(
                <div className={"h-full flex items-center w-1/6 px-1"} key={i}>
                    <NavPopover
                        groupName={navItem.groupName}
                        pages={navItem.pages}
                        updateMainContent={updateMainContent}
                    />
                </div>
            )
        }
    }

    function handleBackClick() {
        setStartIndex(startIndex - 1)
    }

    function handleFrontClick() {
        setStartIndex(startIndex + 1)
    }


    return (
        <div className={"w-full flex flex-row justify-between mx-14"}>
            <button
                className={resolveClassNames(
                    "w-10 ",
                    leftToggleable ? "" : ""
                )}
                onClick={handleBackClick}
                disabled={(!leftToggleable)}
            >
                <ChevronLeftIcon className={resolveClassNames(
                    "h-full w-full text-gray-600 ",
                    leftToggleable ? "text-white" : ""
                )}/>
            </button>

            <div className={"w-full flex justify-start items-baseline px-4 py-2"}>
                {popovers}
            </div>

            <button
                className={resolveClassNames(
                    "w-10",
                    rightToggleable ? "" : ""
                )}
                onClick={handleFrontClick}
                disabled={!rightToggleable}
            >
                <ChevronRightIcon className={resolveClassNames(
                    "h-full w-full text-gray-600",
                    rightToggleable ? "text-white" : ""
                )}/>
            </button>
        </div>
    )
}