import {defaultUserSummary, sessionManager, SessionSummary} from "../SessionManager";
import {CachedSessionInfo} from "../Pages/other/LoadingPage";
import {createSession} from "./APIHelpers";

export async function destroySession() {
    sessionManager.deactivateApp();
}

export async function switchToGuestSession() {
    let createSessionResponse = await createSession();

    if (!createSessionResponse.success) {
        throw new Error(createSessionResponse.message);
    }

    let sessionSummary: SessionSummary = createSessionResponse.data;

    sessionManager.activateApp(
        sessionSummary.sessionId,
        sessionSummary.isGuestSession,
        sessionSummary.userId,
        sessionSummary.groups,
        sessionSummary.accessRights,
        defaultUserSummary.firstname,
        defaultUserSummary.lastname,
        defaultUserSummary.email,
        defaultUserSummary.imageUrl
    );

    let cachedSessionInfo: CachedSessionInfo = {
        sessionId: sessionSummary.sessionId,
        isGuestSession: sessionSummary.isGuestSession,
        userId: sessionSummary.userId,
        groups: sessionSummary.groups,
        accessRights: sessionSummary.accessRights,
        firstname: defaultUserSummary.firstname,
        lastname: defaultUserSummary.lastname,
        email: defaultUserSummary.email,
        imageUrl: defaultUserSummary.imageUrl
    }

    localStorage.setItem("session-info", JSON.stringify(cachedSessionInfo));
}

export function resolveClassNames(...classes: string[]) {
    return classes.filter(Boolean).join(' ')
}