import {Accordion, AccordionItem as Item} from "@szhsin/react-accordion";
import {ChevronUpIcon} from "@heroicons/react/24/outline";
import {resolveClassNames} from "../../Helpers/ApplicationHelpers";
import {Disclosure} from "@headlessui/react";
import {Page, PageGroup} from "./NavBar";

/**
 * @type {React.ExoticComponent<import('@szhsin/react-accordion').AccordionItemProps>}
 */

export function AccordionItem({groupName, pages, updateMainContent}: { groupName: string, pages: Page[], updateMainContent: (page: Page) => void}) {
    return (
        <Item
            header={({state: {isEnter}}) => (
                <>
                    <span>{groupName}</span>

                    <div className="flex items-center h-fit px-2">
                        <ChevronUpIcon
                            className={resolveClassNames(
                                "h-6 w-6 text-purple-500 transition-transform duration-200 ease-in-out",
                                isEnter ? "rotate-180 transform" : ""
                            )}
                        />
                    </div>
                </>
            )}

            buttonProps={
                {
                    className: ({isEnter}) => resolveClassNames(
                        "flex w-full justify-between rounded py-3 px-3 hover:text-gray-100 hover:bg-gray-800"
                    )
                }
            }

            contentProps={{
                className: resolveClassNames(
                    "transition-height duration-200 ease-in-out"
                )
            }}

            panelProps={{className: "flex-col flex space-y-2"}}
        >

            {pages.map((page) => (
                <Disclosure.Button
                    key={page.pageName}
                    className={resolveClassNames(
                        'hover:bg-gray-800 hover:text-gray-100',
                        'block rounded-md px-7 py-3 text-base text-left'
                    )}
                    onClick={() => (updateMainContent(page))}
                >
                    {page.pageName}
                </Disclosure.Button>
            ))}
        </Item>
    )
}


export default function NavAccordion({userNavigation, updateMainContent}: {userNavigation: PageGroup[], updateMainContent: (page: Page) => void }) {

    return (
        <div className="space-y-1 px-2 pb-3 pt-2 sm:px-3 font-medium">
            <Accordion transition transitionTimeout={200}>

                {userNavigation.map((pageGroup, index) => (
                    <AccordionItem
                        key={index}
                        groupName={pageGroup.groupName}
                        pages={pageGroup.pages}
                        updateMainContent={updateMainContent}
                    />
                ))}
            </Accordion>
        </div>
    );
}
