import React, {useEffect, useState} from "react";
import {resolveClassNames} from "../../Helpers/ApplicationHelpers";
import {createUser, getApplicationGroups} from "../../Helpers/APIHelpers";
import AvatarSelector, {AvatarInfo, getDefaultAvatarInfo} from "../../Components/AvatarSelector";
import MessageIndicator, {
    getDefaultMessageIndicatorInfo,
} from "../../Components/MessageIndicator";


interface GroupCheckbox {
    groupName: string,
    groupReadableName: string,
    groupDescription: string
    checked: boolean
}

type Group = Pick<GroupCheckbox, "groupName" | "groupReadableName" | "groupDescription">;

interface FormData {
    firstname: string
    lastname: string
    email: string
    password: string
    confirmPassword: string
    groupCheckBoxes: GroupCheckbox[]
    readonly: boolean
}

interface UserCreationInfo {
    firstname: string
    lastname: string
    email: string
    password: string
    confirmPassword: string
    groups: string[]
}

function getUserCreationInfo(data: FormData): UserCreationInfo {
    let groupNames: string[] = [];

    for (let i = 0; i < data.groupCheckBoxes.length; i++) {
        let groupCheckbox = data.groupCheckBoxes[i];

        if (!groupCheckbox.checked) {
            continue
        }

        groupNames.push(groupCheckbox.groupName);
    }

    return {
        firstname: data.firstname,
        lastname: data.lastname,
        email: data.email,
        password: data.password,
        confirmPassword: data.confirmPassword,
        groups: groupNames
    }
}


let initialGroupCheckBoxes: GroupCheckbox[] = [];

function getDefaultFormData(): FormData {

    return {
        firstname: "",
        lastname: "",
        email: "",
        password: "",
        confirmPassword: "",
        groupCheckBoxes: [],
        readonly: false
    }
}


export default function CreateUserPage() {

    const [formData, setFormData] = useState(getDefaultFormData());
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo());
    const [avatarInfo, setAvatarInfo] = useState(getDefaultAvatarInfo());
    const [requestOngoing, setRequestOngoing] = useState(false);


    useEffect(() => {

        getApplicationGroups().then((response) => {
            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                return
            }

            let groups: Group[] = response.data
            let groupCheckBoxes: GroupCheckbox[]

            groupCheckBoxes = groups.map((group) => {

                let groupCheckbox: GroupCheckbox = {
                    groupName: group.groupName,
                    groupReadableName: group.groupReadableName,
                    groupDescription: group.groupDescription,
                    checked: false
                }

                return (
                    groupCheckbox
                )
            });

            initialGroupCheckBoxes = groupCheckBoxes;

            setFormData({
                ...formData,
                groupCheckBoxes: structuredClone(groupCheckBoxes)
            })

        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        })
    }, []);

    let inputFields = [
        formData.firstname,
        formData.lastname,
        formData.email,
        formData.password,
        formData.confirmPassword
    ]

    let inputsAreValid = true;
    let checkBoxesAreValid = false;

    for (const inputField of inputFields) {
        if (inputField.length < 1) {
            inputsAreValid = false
            break
        }
    }

    if (formData.groupCheckBoxes.length > 0) {
        for (const checkbox of formData.groupCheckBoxes) {
            if (checkbox.checked) {
                checkBoxesAreValid = true
                break
            }
        }
    }

    let submitButtonIsActive = (inputsAreValid && checkBoxesAreValid) && (!formData.readonly) && (!requestOngoing) && (avatarInfo.imageIsSet);

    function handleInputChange(event: React.ChangeEvent<HTMLInputElement>) {
        const {name, value} = event.target;

        let newFormData = {
            ...formData,
            [name]: value
        }
        setFormData(newFormData);
    }

    function handleBoxCheck(event: React.ChangeEvent<HTMLInputElement>) {

        const {name, checked} = event.target
        let newGroupCheckBoxes: GroupCheckbox[] = formData.groupCheckBoxes.map((groupCheckBox) => {
            if (groupCheckBox.groupName === name) {
                return {
                    ...groupCheckBox,
                    checked: checked
                };
            }

            else return groupCheckBox;
        })


        let newFormData: FormData = {
            ...formData,
            groupCheckBoxes: newGroupCheckBoxes
        }

        setFormData(newFormData);
    }

    async function handleSubmit(event: React.FormEvent<HTMLFormElement>) {
        event.preventDefault();

        let userCreationInfo = getUserCreationInfo(formData);

        setMessageIndicatorInfo(getDefaultMessageIndicatorInfo);
        setRequestOngoing(true);


        createUser(
            userCreationInfo.firstname,
            userCreationInfo.lastname,
            userCreationInfo.email,
            userCreationInfo.password,
            userCreationInfo.confirmPassword,
            userCreationInfo.groups,
            avatarInfo.imageData
        ).then((response) => {
            setRequestOngoing(false);

            setMessageIndicatorInfo({
                state: response.success ? "success" : "error",
                message: response.message
            })

            if (response.success) {
                let newFormData: FormData = {
                    ...formData,
                    readonly: true
                }

                setFormData(newFormData)
            }
        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            setRequestOngoing(false);
            return
        })
    }

    function clearFields() {
        setFormData({
            ...getDefaultFormData(),
            groupCheckBoxes: initialGroupCheckBoxes
        });

        setMessageIndicatorInfo(getDefaultMessageIndicatorInfo);
        setAvatarInfo(getDefaultAvatarInfo);
    }

    function updatePreview(avatarInfo: AvatarInfo) {
        setAvatarInfo(avatarInfo);
    }

    return (
        <form autoComplete={"off"} onSubmit={(event) => handleSubmit(event)}>
            <div className="space-y-12">

                <div className="border-b border-gray-700 pb-12">
                    <h2 className="text-2xl font-semibold leading-7">Personal Information</h2>
                    <p className="mt-1 text-sm leading-6">
                        This information will be used to identify the user.
                    </p>


                    <div className="mt-10">
                        <AvatarSelector imageIsSet={avatarInfo.imageIsSet} imageData={avatarInfo.imageData} updatePreview={updatePreview} readonly={formData.readonly}/>
                    </div>

                    <div className="mt-10 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 gap-x-6 gap-y-8">

                        <div className={""}>
                            <label htmlFor="firstname" className="block text-sm font-medium leading-6">
                                First Name
                            </label>
                            <div className="mt-2">
                                <div
                                    className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600 px-2">

                                    <input
                                        type="text"
                                        name="firstname"
                                        id="firstname"
                                        value={formData.firstname}
                                        className="block flex-1 border-0 bg-transparent py-1.5 pl-1 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6"
                                        placeholder="Ben"
                                        maxLength={60}
                                        onChange={(event) => handleInputChange(event)}
                                        disabled={formData.readonly}
                                        required={true}
                                    />
                                </div>
                            </div>
                        </div>

                        <div className={""}>
                            <label htmlFor="lastname" className="block text-sm font-medium leading-6">
                                Last Name
                            </label>
                            <div className="mt-2">
                                <div
                                    className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600 px-2">

                                    <input
                                        type="text"
                                        name="lastname"
                                        id="lastname"
                                        value={formData.lastname}
                                        className="block flex-1 border-0 bg-transparent py-1.5 pl-1 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6"
                                        placeholder="Tennyson"
                                        maxLength={60}
                                        onChange={(event) => handleInputChange(event)}
                                        disabled={formData.readonly}
                                        required={true}
                                    />
                                </div>
                            </div>
                        </div>


                        <div className={""}>
                            <label htmlFor="email" className="block text-sm font-medium leading-6">
                                Email
                            </label>
                            <div className="mt-2">
                                <div
                                    className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600 px-2">

                                    <input
                                        type="email"
                                        name="email"
                                        id="email"
                                        value={formData.email}
                                        className="w-10 block flex-1 border-0 bg-transparent py-1.5 pl-1 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6"
                                        placeholder="ben.tennyson@aun.edu.ng"
                                        onChange={(event) => handleInputChange(event)}
                                        disabled={formData.readonly}
                                        required={true}
                                    />

                                </div>
                            </div>
                        </div>

                        <div className={""}>
                            <label htmlFor="password" className="block text-sm font-medium leading-6">
                                Password
                            </label>
                            <div className="mt-2">
                                <div
                                    className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600 px-2">

                                    <input
                                        type="password"
                                        name="password"
                                        id="password"
                                        value={formData.password}
                                        className="block flex-1 border-0 bg-transparent py-1.5 pl-1  placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6"
                                        placeholder="Minimum of 8 characters"
                                        maxLength={60}
                                        onChange={(event) => handleInputChange(event)}
                                        disabled={formData.readonly}
                                        required={true}
                                    />
                                </div>
                            </div>
                        </div>

                        <div className={""}>
                            <label htmlFor="confirmPassword" className="block text-sm font-medium leading-6 ">
                                Confirm Password
                            </label>
                            <div className="mt-2">
                                <div
                                    className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600 px-2">

                                    <input
                                        type={"password"}
                                        name="confirmPassword"
                                        id="confirmPassword"
                                        value={formData.confirmPassword}
                                        className="block flex-1 border-0 bg-transparent py-1.5 pl-1  placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6"
                                        placeholder="Must match Password field"
                                        maxLength={60}
                                        onChange={(event) => handleInputChange(event)}
                                        disabled={formData.readonly}
                                        required={true}
                                    />
                                </div>
                            </div>
                        </div>

                    </div>
                </div>


                <div className="border-b border-gray-700 pb-12">
                    <h2 className="text-2xl font-semibold leading-7">Access Control</h2>
                    <p className="mt-1 text-sm leading-6">
                        Select one or more groups that you want this user to belong to.
                    </p>

                    <div className="mt-10 space-y-10">
                        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 mt-6 gap-y-3 gap-x-3">

                            {formData.groupCheckBoxes.map((group, i) => (
                                <div className="flex border border-gray-700 rounded-md h-20" key={i}>
                                    <div className="flex justify-center items-center w-2/12">
                                        <input
                                            type="checkbox"
                                            id={group.groupName}
                                            name={group.groupName}
                                            checked={group.checked}
                                            className="h-6 w-6 rounded border-gray-300 text-indigo-600 focus:ring-indigo-600"
                                            onChange={(event) => handleBoxCheck(event)}
                                            disabled={formData.readonly}
                                        />
                                    </div>
                                    <div className="text-sm leading-6 w-10/12 px-2 flex flex-col justify-center">
                                        <label htmlFor="comments" className="font-medium">
                                            {group.groupReadableName}
                                        </label>
                                        <p className="overflow-y-scroll">{group.groupDescription}</p>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>


            <div className={"mt-5"}>
                <MessageIndicator
                    state={messageIndicatorInfo.state}
                    message={messageIndicatorInfo.message}
                />
            </div>


            <div className="mt-6 flex items-center justify-end gap-x-6">
                <button
                    type={"button"}
                    className={resolveClassNames(
                        "border border-gray-700 rounded-md  px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600",
                    )}
                    onClick={clearFields}
                >
                    Clear Fields
                </button>

                <button
                    type="submit"
                    disabled={!submitButtonIsActive}
                    className={resolveClassNames(
                        "rounded-md  px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600",
                        submitButtonIsActive ? "bg-indigo-600 hover:bg-indigo-500 text-white" : "bg-gray-600 hover:bg-gray-600"
                    )}
                >
                    Create User
                </button>
            </div>
        </form>
    )
}
