import React, {useEffect, useState} from "react";
import MessageIndicator, {getDefaultMessageIndicatorInfo} from "../../Components/MessageIndicator";
import PosTerminalIcon from "../../assets/images/pos-terminal-icon.svg";
import {resolveClassNames} from "../../Helpers/ApplicationHelpers";
import {getCoffeeShopPosStatus} from "../../Helpers/APIHelpers";
import {Tab} from "@headlessui/react";
import {CoffeeShopItem, coffeeShopItems, coffeeShopItemTypes} from "./coffee-shop-items";
import {CafItem, CafItemReview, CafMenu} from "../Cafeteria/CafMenuPage";
import {Rating} from "@mui/material";


function CoffeeShopItemCard({coffeeShopItem}: {coffeeShopItem: CoffeeShopItem}) {


    let statusClass: string;

    switch (coffeeShopItem.quantityLeft) {
        case 0: {
            statusClass = "bg-gray-500"
            break
        }

        case 1:
        case 2:
        case 3:
        case 4:
        case 5: {
            statusClass = "bg-red-500"
            break
        }
        case 6:
        case 7:
        case 8:
        case 9:
        case 10 : {
            statusClass = "bg-yellow-500"
            break
        }

        default : {
            statusClass = "bg-green-500"
        }
    }


    return (
        <div className={"rounded-2xl border border-gray-800 overflow-hidden"}>
            <div className={"flex flex-col h-64 w-64 md:w-60"}>
                <div className={"h-2/3"}>
                    <img
                        src={coffeeShopItem.imageUrl}
                        alt={coffeeShopItem.name}
                        className={"w-full h-full object-cover"}
                    />
                </div>
                <div className={"h-1/3 flex flex-col justify-center items-start"}>
                    <div className={"h-4/6 px-2 flex justify-between w-full items-center"}>
                        <div className={"font-semibold text-base"}>{coffeeShopItem.name}</div>
                        <div className={"font-semibold"}>{coffeeShopItem.price}</div>
                    </div>
                    <div
                        className={`w-full flex justify-center items-center h-2/6 px-2 text-sm font-semibold text-white ${statusClass}`}>
                        {coffeeShopItem.quantityLeft} Left
                    </div>
                </div>
            </div>
        </div>
    )
}

function CoffeeShopItemsHolder({items}: {items: CoffeeShopItem[]}) {

    return (
        <div className={"mt-5"}>

            {items.length > 0 ?
                <div
                    className={" py-10 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 mx-auto border rounded-2xl border-gray-700 p-5 mt-5 justify-items-center gap-y-10"}>
                    {items.map((coffeeShopItem, index) => (
                        <CoffeeShopItemCard
                            coffeeShopItem={coffeeShopItem}
                            key={index}
                        />))}
                </div>

                :

                <div className={"p-5"}>There are no items in this section</div>
            }
        </div>
    )
}


export default function CoffeeShopMenuPage() {
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo());
    const [coffeeShopPosIsActive, setCoffeeShopPosIsActive] = useState(false);

    const [snacks, setSnacks] = useState<CoffeeShopItem[]>([]);
    const [drinks, setDrinks] = useState<CoffeeShopItem[]>([]);



    useEffect(() => {
        getCoffeeShopPosStatus().then((response) => {
            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                return
            }

            setCoffeeShopPosIsActive(response.data.isActive);
        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
        })

        let snacksArray: CoffeeShopItem[] = [];
        let drinksArray: CoffeeShopItem[] = []

        for (const item of coffeeShopItems) {
            if (item.type === "snack") {
                snacksArray.push(item)
            }

            else if (item.type === "drink") {
                drinksArray.push(item)
            }
        }

        setSnacks(snacksArray);
        setDrinks(drinksArray);

    }, [])

    return (
        <div>
            <div className={"flex flex-col space-y-3"}>
                <div className={""}>
                    <img
                        src={PosTerminalIcon}
                        className={"h-24"}
                        alt={"Pos Terminal Icon"}
                    />
                </div>

                <div className={"flex flex-col space-y-0.5"}>
                    <p className={"font-semibold"}>Coffee Shop POS Status</p>

                    <div className={"flex"}>
                        <p className={"font-semibold"}>{coffeeShopPosIsActive ? "Active" : "Inactive"}</p>
                        <div className={"flex justify-center items-center ml-3"}>
                            <div className={resolveClassNames(
                                "w-3 h-3 rounded-full",
                                coffeeShopPosIsActive ? "bg-green-500" : "bg-red-500"
                            )}></div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="w-full sm:px-0 mt-10">
                <Tab.Group>
                    <Tab.List className="flex space-x-1 rounded-xl p-1 bg-gray-800">
                        {coffeeShopItemTypes.map((itemType, index) => (
                            <Tab
                                key={index}
                                className={({selected}) =>
                                    resolveClassNames(
                                        'w-full rounded-lg py-2.5 text-sm font-medium leading-5',
                                        selected ? 'bg-gray-700' : 'text-blue-100 hover:bg-white/[0.12] hover:text-white'
                                    )
                                }
                            >
                                {itemType}
                            </Tab>
                        ))}
                    </Tab.List>
                    <Tab.Panels className="mt-2">
                        <Tab.Panel className={resolveClassNames('rounded-xl p-3')}>
                            <CoffeeShopItemsHolder
                                items={snacks}
                            />
                        </Tab.Panel>

                        <Tab.Panel className={resolveClassNames('rounded-xl p-3')}>
                            <CoffeeShopItemsHolder
                                items={drinks}
                            />
                        </Tab.Panel>
                    </Tab.Panels>
                </Tab.Group>
            </div>
            <div className={"mt-5 rounded"}>
                <MessageIndicator
                    state={messageIndicatorInfo.state}
                    message={messageIndicatorInfo.message}
                />
            </div>
        </div>
    )
}