import {Menu, Transition} from "@headlessui/react";
import {Fragment} from "react";
import {destroySession, resolveClassNames} from "../../Helpers/ApplicationHelpers";
import {AppInfo, sessionManager, signInPage, userProfilePage, UserSummary} from "../../SessionManager";
import {Page} from "./NavBar";
import {switchToGuestSession} from "../../Helpers/ApplicationHelpers";

export default function DesktopUserNav({isGuestSession, userSummary, updateMainContent, refreshApp}: {isGuestSession: boolean, userSummary: UserSummary, updateMainContent: (page: Page) => void, refreshApp: (appInfo: AppInfo) => void}) {

    function signOut() {
        destroySession().then(() => {
            switchToGuestSession().then(() => {
                refreshApp(sessionManager.getAppInfo());
            })
        })
    }

    return (
        <Menu as="div" className="relative">
            <div>
                <Menu.Button
                    className="flex max-w-xs items-center rounded-full text-sm focus:outline-none focus:ring-2 focus:ring-gray-200 focus:ring-offset-2 focus:ring-offset-gray-800">
                    <span className="sr-only">Open user menu</span>
                    <div className={"h-10 w-10"}>
                        <img className="h-full w-full rounded-full" src={userSummary.imageUrl} alt="User Avatar"/>
                    </div>
                </Menu.Button>
            </div>
            <Transition
                as={Fragment}
                enter="transition ease-out duration-100"
                enterFrom="transform opacity-0 scale-95"
                enterTo="transform opacity-100 scale-100"
                leave="transition ease-in duration-75"
                leaveFrom="transform opacity-100 scale-100"
                leaveTo="transform opacity-0 scale-95"
            >
                {isGuestSession ?
                    <Menu.Items className="absolute right-0 z-10 mt-2 w-48 origin-top-right rounded-md bg-gray-800 py-1 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
                        <Menu.Item key={"log-in"}>
                            {({active}) => (
                                <button
                                    onClick={() => updateMainContent(signInPage)}
                                    className={resolveClassNames(
                                        active ? 'bg-gray-700' : '',
                                        'block px-4 py-2 text-sm text-left w-full'
                                    )}
                                >
                                    {"Log In"}
                                </button>
                            )}
                        </Menu.Item>

                    </Menu.Items>

                    :

                    <Menu.Items className="absolute right-0 z-10 mt-2 w-48 origin-top-right rounded-md bg-gray-800 py-1 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
                        <Menu.Item key={"profile"}>
                            {({active}) => (
                                <button
                                    onClick={() => {updateMainContent(userProfilePage)}}
                                    className={resolveClassNames(
                                        active ? 'bg-gray-700' : '',
                                        'block px-4 py-2 text-sm text-left w-full'
                                    )}
                                >
                                    {"Your Profile"}
                                </button>
                            )}
                        </Menu.Item>
                        <Menu.Item key={"sign-out"}>
                            {({active}) => (
                                <button
                                    onClick={signOut}
                                    className={resolveClassNames(
                                        active ? 'bg-gray-700' : '',
                                        'block px-4 py-2 text-sm text-left w-full'
                                    )}
                                >
                                    {"Sign Out"}
                                </button>
                            )}
                        </Menu.Item>
                    </Menu.Items>
                }
            </Transition>
        </Menu>
    )
}