import React, {useState} from "react";
import MessageIndicator, {getDefaultMessageIndicatorInfo} from "../../Components/MessageIndicator";
import {appendImageOrigin, authenticateUser, getOwnUserInfo} from "../../Helpers/APIHelpers";
import {resolveClassNames} from "../../Helpers/ApplicationHelpers";
import {PageProps} from "../../Components/Navigation/NavBar";
import {sessionManager, SessionSummary} from "../../SessionManager";
import {CachedSessionInfo} from "./LoadingPage";

interface FormData {
    email: string,
    password: string,
    staySignedIn: boolean
}

function getDefaultFormData(): FormData {
    return {
        email: "",
        password: "",
        staySignedIn: false
    }
}

export default function SignInPage({refreshApp}: PageProps) {

    const [formData, setFormData] = useState<FormData>(getDefaultFormData);
    const [serverResponseBarInfo, setServerResponseBarInfo] = useState(getDefaultMessageIndicatorInfo);
    const [requestOngoing, setRequestOngoing] = useState(false);


    let inputFields = [
        formData.email,
        formData.password,
    ]

    let inputsAreValid = true;

    for (const inputField of inputFields) {
        if (inputField.length < 1) {
            inputsAreValid = false
            break
        }
    }

    let submitButtonIsActive = (inputsAreValid) && (!requestOngoing);

    function handleInputChange(event: React.ChangeEvent<HTMLInputElement>) {
        const {name, value} = event.target;

        let newFormData = {
            ...formData,
            [name]: value
        }
        setFormData(newFormData);
    }

    function handleBoxCheck(event: React.ChangeEvent<HTMLInputElement>) {
        const {name, checked} = event.target

        let newFormData: FormData = {
            ...formData,
            staySignedIn: checked
        }

        setFormData(newFormData);
    }

    function handleSubmit(event: React.FormEvent<HTMLFormElement>) {
        event.preventDefault();

        setServerResponseBarInfo(getDefaultMessageIndicatorInfo);
        setRequestOngoing(true);

        authenticateUser(
            formData.email,
            formData.password,
            formData.staySignedIn
        ).then((response) => {
            setRequestOngoing(false);

            setServerResponseBarInfo({
                state: response.success ? "success" : "error",
                message: response.message
            });

            if (!response.success) {
                return
            }

            let sessionSummary: SessionSummary = response.data;

            getOwnUserInfo(sessionSummary.userId).then((response) => {
                setServerResponseBarInfo({
                    state: response.success ? "success" : "error",
                    message: response.message
                });

                if (!response.success) {
                    return
                }

                let userDetails: {
                    lastname: string;
                    firstname: string,
                    email: string,
                    imageUrl: string
                } = response.data;

                sessionManager.activateApp(
                    sessionSummary.sessionId,
                    sessionSummary.isGuestSession,
                    sessionSummary.userId,
                    sessionSummary.groups,
                    sessionSummary.accessRights,
                    userDetails.firstname,
                    userDetails.lastname,
                    userDetails.email,
                    appendImageOrigin(`${userDetails.imageUrl}`)
                );

                let cachedSessionInfo: CachedSessionInfo = {
                    sessionId: sessionSummary.sessionId,
                    isGuestSession: sessionSummary.isGuestSession,
                    userId: sessionSummary.userId,
                    groups: sessionSummary.groups,
                    accessRights: sessionSummary.accessRights,
                    firstname: userDetails.firstname,
                    lastname: userDetails.lastname,
                    email: userDetails.email,
                    imageUrl: appendImageOrigin(`${userDetails.imageUrl}`)
                }

                localStorage.setItem("session-info", JSON.stringify(cachedSessionInfo));

                refreshApp(sessionManager.getAppInfo());
            })

        }).catch((e) => {
            let err = e as Error
            setServerResponseBarInfo({
                state: "error",
                message: err.message
            });
            setRequestOngoing(false);
            return
        })
    }

    return (
        <>
            <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
                <div className="sm:mx-auto sm:w-full sm:max-w-sm">

                    <h2 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight">
                        Sign in to your account
                    </h2>
                </div>

                <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
                    <form className="space-y-6" autoComplete={"on"} onSubmit={(event) => {
                        handleSubmit(event)
                    }}>
                        <div>
                            <label htmlFor="email" className="block text-sm font-medium leading-6">
                                Email address
                            </label>
                            <div className="mt-2">
                                <input
                                    id="email"
                                    name="email"
                                    type="email"
                                    autoComplete="email"
                                    required
                                    className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                    onChange={(event) => handleInputChange(event)}
                                />
                            </div>
                        </div>

                        <div>
                            <div className="flex items-center justify-between">
                                <label htmlFor="password" className="block text-sm font-medium leading-6">
                                    Password
                                </label>

                            </div>
                            <div className="mt-2">
                                <input
                                    id="password"
                                    name="password"
                                    type="password"
                                    autoComplete="current-password"
                                    required
                                    className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                    onChange={(event) => handleInputChange(event)}
                                />
                            </div>
                        </div>

                        <div>
                            <button
                                type="submit"
                                className={resolveClassNames(
                                    "flex w-full justify-center rounded-md  px-3 py-1.5 text-sm font-semibold leading-6 shadow-sm focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600",
                                    submitButtonIsActive ? "bg-indigo-600 hover:bg-indigo-500 text-white" : "bg-gray-600 hover:bg-gray-600"
                                )}
                            >
                                Sign in
                            </button>
                        </div>

                        <div className="mt-10 text-sm">
                            <input
                                type="checkbox"
                                id={"staySignedIn"}
                                name={"staySignedIn"}
                                checked={formData.staySignedIn}
                                className="h-5 w-5 rounded border-gray-300 text-indigo-600 focus:ring-indigo-600"
                                onChange={(event) => handleBoxCheck(event)}
                            />

                            <span className={"ml-2"}>Stay Signed In?</span>

                        </div>

                    </form>
                </div>

                <div className={"mt-5"}>
                    <MessageIndicator
                        state={serverResponseBarInfo.state}
                        message={serverResponseBarInfo.message}
                    />
                </div>
            </div>
        </>
    )
}
