import NavBar, {Page} from "./Components/Navigation/NavBar";
import MainContent from "./Components/MainContent";
import Footer from "./Components/Footer";
import NetworkStatusBar from "./Components/NetworkStatusBar";
import {useState} from "react";
import {AppInfo, loadingPage, sessionManager} from "./SessionManager";


export default function App() {
    const [appInfo, setAppInfo] = useState(sessionManager.getAppInfo());
    const [currentPage, setCurrentPage] = useState<Page>(loadingPage);

    function updateMainContent(page: Page) {
        setCurrentPage(page)
    }

    function refreshApp(appInfo: AppInfo) {
        setAppInfo(appInfo);

        const page = sessionManager.userNavigation[0].pages[0]

        setCurrentPage({
            pageName: page.pageName,
            pageDescription: page.pageDescription,
            pageContent: page.pageContent,
            pageIcon: page.pageIcon,
            requiredAccessRights: page.requiredAccessRights
        })
    }

    return (
        <>
            <div className="min-h-screen bg-gray-900 text-gray-200 flex flex-col">
                {sessionManager.isSessionActive &&
                    <NavBar
                        isGuestSession={appInfo.isGuestSession}
                        userSummary={{
                            firstname: appInfo.firstname,
                            lastname: appInfo.lastname,
                            email: appInfo.email,
                            imageUrl: appInfo.imageUrl
                        }}
                        userNavigation={appInfo.navigation}
                        refreshApp={refreshApp}
                        updateMainContent={updateMainContent}
                    />}
                <NetworkStatusBar/>
                <MainContent
                    heading= {currentPage.pageName}
                    // Content={ManageCafWarehouseItemsPage}
                    Content={currentPage.pageContent}
                    refreshApp={refreshApp}
                    updateMainContent={updateMainContent}
                />
                <Footer />
            </div>
        </>
    )
}
