import React, {useEffect, useState} from "react";
import MessageIndicator, {getDefaultMessageIndicatorInfo} from "../../Components/MessageIndicator";
import {checkIfCafMenuExists} from "../../Helpers/APIHelpers";
import {PageProps} from "../../Components/Navigation/NavBar";
import {createCafMenuPage, deleteCafMenuPage} from "../../SessionManager";


export default function ManageCafeteriaMenuPage({updateMainContent}: PageProps) {
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo());

    useEffect(() => {
        checkIfCafMenuExists().then((response) => {

            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                });
                return
            }

            let menuExists = response.data.cafMenuExists

            if (menuExists) {
                updateMainContent(deleteCafMenuPage);

            } else {
                updateMainContent(createCafMenuPage);
            }

        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        })
    }, [])

    return (
        <div>
            <div className={"mt-5 rounded"}>

                <MessageIndicator
                    state={messageIndicatorInfo.state}
                    message={messageIndicatorInfo.message}
                />
            </div>
        </div>
    )
}