import MessageIndicator, {getDefaultMessageIndicatorInfo} from "../../Components/MessageIndicator";
import React, {useEffect, useState} from "react";
import {validateSession} from "../../Helpers/APIHelpers";
import {ModelPermission, sessionManager} from "../../SessionManager";
import {PageProps} from "../../Components/Navigation/NavBar";
import {switchToGuestSession} from "../../Helpers/ApplicationHelpers";

export interface CachedSessionInfo {
    sessionId: string,
    isGuestSession: boolean,
    userId: string,
    groups: string[],
    accessRights: ModelPermission[],
    firstname: string,
    lastname: string,
    email: string,
    imageUrl: string
}

export default function LoadingPage({refreshApp}: PageProps) {
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo);

    useEffect(() => {

        let cachedSessionSummaryString = localStorage.getItem("session-info");

        if (cachedSessionSummaryString) {

            let cachedSessionSummary: CachedSessionInfo = JSON.parse(cachedSessionSummaryString);

            validateSession(cachedSessionSummary.sessionId).then((response) => {

                if (response.success) {
                    sessionManager.activateApp(
                        cachedSessionSummary.sessionId,
                        cachedSessionSummary.isGuestSession,
                        cachedSessionSummary.userId,
                        cachedSessionSummary.groups,
                        cachedSessionSummary.accessRights,
                        cachedSessionSummary.firstname,
                        cachedSessionSummary.lastname,
                        cachedSessionSummary.email,
                        cachedSessionSummary.imageUrl
                    );

                    refreshApp(sessionManager.getAppInfo());
                } else {
                    setMessageIndicatorInfo({
                        state: "error",
                        message: "Cached session info was not valid. Creating new session . . ."
                    });

                    createGuestSession();
                }
            }).catch((e) => {
                let err = e as Error

                setMessageIndicatorInfo({
                    state: "error",
                    message: err.message
                });
            })

        } else {
            createGuestSession();
        }
        try {

        }
        catch (e) {
            let err = e as Error

            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        }

    }, []);


    function createGuestSession() {
        switchToGuestSession().then(() => {
            refreshApp(sessionManager.getAppInfo());
        }).catch((e) => {
            let err = e as Error

            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
        })
    }


    return (
        <div>
            <div className={"mt-5"}>
                <MessageIndicator
                    state={messageIndicatorInfo.state}
                    message={messageIndicatorInfo.message}
                />
            </div>
        </div>
    )
}