import React from "react";
import {resolveClassNames} from "../Helpers/ApplicationHelpers";

export function getDefaultImageInfo(): ImageInfo {
    return {
        imageIsSet: false,
        imageData: "",
        mime: ""
    }
}

export interface ImageInfo {
    imageIsSet: boolean,
    imageData: string,
    mime: string
}

export default function ImageSelector({imageIsSet, imageData, updatePreview, readonly = false}: { imageIsSet: boolean, imageData: string, updatePreview(avatarInfo: ImageInfo): void, readonly: boolean}) {

    function previewImage(event: React.ChangeEvent<HTMLInputElement>) {
        const imageFiles = event.target.files;

        if (!imageFiles) {
            return
        }

        const blob = imageFiles[0];

        let imageData: string = "";

        let reader = new FileReader();


        reader.onload = () => {
            imageData = reader.result as string
            const avatarInfo: ImageInfo = {
                imageIsSet: true,
                imageData: imageData,
                mime: blob.type
            }

            updatePreview(avatarInfo);
        }

        reader.readAsDataURL(imageFiles[0])
    }

    function clearPreview() {
        updatePreview(getDefaultImageInfo())
    }

    return (
        <div className={"flex flex-col w-fit"}>
            <div>
                {
                    imageIsSet ?
                        <img
                            src={imageData}
                            alt={"User Avatar Preview"}
                            className={"h-52 w-52 object-cover border border-gray-800"}
                            aria-hidden="true"
                        />
                        :
                        <div
                            className={"h-52 w-52 object-cover border border-gray-800 flex justify-center items-center"}
                            aria-hidden="true"
                        >
                            <p>Upload Image Here</p>
                        </div>
                }
            </div>

            <div className={"flex space-x-5 mt-5 justify-between"}>
                <button
                    type={"button"}
                    onClick={clearPreview}
                    className={"border border-gray-700 rounded-md  px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"}
                >
                    Remove
                </button>

                <label
                    role={"button"}

                    className={resolveClassNames(
                        "rounded-md px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600",
                        readonly ? "bg-gray-600 hover:bg-gray-600" : "bg-indigo-600 hover:bg-indigo-500 text-white"
                    )}
                    htmlFor={"file-input"}>{imageIsSet ? "Change" : "Upload"}
                </label>

                <input
                    id={"file-input"}
                    disabled={readonly}
                    className={"rounded-md border border-gray-700 px-2.5 py-1.5 text-sm font-semibold shadow-sm ring-1 ring-inset ring-gray-300 hidden"}
                    type={"file"} accept={"image/png image/jpeg"}
                    onChange={(event) => previewImage(event)}
                />
            </div>
        </div>
    )
}