import {resolveClassNames} from "../Helpers/ApplicationHelpers";
import React from "react";

export type ServerResponseState = "waiting" | "success" | "error";

export interface ServerResponseBarInfo {
    state: ServerResponseState,
    message: string
}

export function getDefaultMessageIndicatorInfo(): ServerResponseBarInfo {
    return {
        state: "waiting",
        message: ""
    }
}

export default function MessageIndicator({state, message}: { state: ServerResponseState, message: string }) {
    return (
        <div
            className={resolveClassNames(
                "h-6 rounded text-white text-sm font-semibold flex justify-center items-center",
                state === "success" ? "bg-green-500" : "",
                state === "error" ? "bg-red-500" : ""
            )}
        >
            {message}
        </div>
    )
}