import {useState} from 'react'
import {Popover} from '@headlessui/react'
import {usePopper} from "react-popper";
import {Page} from "./NavBar";


export default function NavPopover({groupName, pages, updateMainContent}: {groupName: string, pages: Page[], updateMainContent: (page: Page) => void}) {
    let [referenceElement, setReferenceElement] = useState<HTMLButtonElement | null>(null);
    let [popperElement, setPopperElement] = useState<HTMLDivElement | null>(null);
    let {styles, attributes} = usePopper(referenceElement, popperElement);


    return (
        <Popover className={"w-full flex justify-center h-full bg-gray-800 rounded"}>
            <Popover.Button
                ref={setReferenceElement}
                className="inline-flex items-center gap-x-1 text-sm font-semibold leading-6 text-white w-full justify-center">
                <span>{groupName}</span>
            </Popover.Button>

            <Popover.Panel
                className={"mt-5 rounded-3xl bg-gray-800 text-sm leading-6 shadow-lg ring-1 ring-gray-900/5 max-h-96 flex flex-row w-96 flex-wrap overflow-y-scroll z-10"}
                ref={setPopperElement}
                style={styles.popper}
                {...attributes.popper}
            >
                {pages.map((page) => (
                    <button
                        key={page.pageName}
                         className="group relative flex gap-x-6 rounded-lg p-4 hover:bg-gray-600 w-96 text-left"
                         onClick={() => (updateMainContent(page))}
                    >
                        <div
                            className="mt-1 flex h-11 w-11 flex-none items-center justify-center rounded-lg bg-gray-700">
                            <page.pageIcon className="h-6 w-6 group-hover:text-indigo-500"
                                       aria-hidden="true"/>
                        </div>
                        <div>
                            <div className="font-semibold text-left">
                                {page.pageName}
                                <span className="absolute inset-0"/>
                            </div>
                            <p className="mt-1 text-gray-400">{page.pageDescription}</p>
                        </div>
                    </button>
                ))}
            </Popover.Panel>
        </Popover>
    )
}
