import {useState} from "react";

export default function NetworkStatusBar() {
    const [isOnline, setIsOnline] = useState(true)

    return (
        <div className={"h-5 sticky top-0 flex justify-center items-center font-semibold text-sm"}>
            {
                !isOnline && (
                    <button className={"bg-red-500 w-full"}>
                        {!isOnline && <p>Currently offline. Information may be outdated</p>}
                    </button>
                )
            }
        </div>

    )
}