import React from "react";
import defaultAvatar from "../assets/images/default-avatar.svg"
import {resolveClassNames} from "../Helpers/ApplicationHelpers";


export function getDefaultAvatarInfo(): AvatarInfo {
    return {
        imageIsSet: false,
        imageData: "",
        mime: ""
    }
}

export interface AvatarInfo {
    imageIsSet: boolean,
    imageData: string,
    mime: string
}

export default function AvatarSelector({imageIsSet, imageData, updatePreview, readonly}: { imageIsSet: boolean, imageData: string, updatePreview(avatarInfo: AvatarInfo): void, readonly: boolean}) {

    function previewAvatar(event: React.ChangeEvent<HTMLInputElement>) {
        const imageFiles = event.target.files;

        if (!imageFiles) {
            return
        }

        const blob = imageFiles[0];

        let imageData: string = "";

        let reader = new FileReader();


        reader.onload = () => {
            imageData = reader.result as string
            const avatarInfo: AvatarInfo = {
                imageIsSet: true,
                imageData: imageData,
                mime: blob.type
            }

            updatePreview(avatarInfo);
        }

        reader.readAsDataURL(imageFiles[0])
    }

    function clearPreview() {
        updatePreview(getDefaultAvatarInfo())
    }

    return (
        <div className={"flex flex-col"}>
            {
                imageIsSet ?
                    <img
                        src={imageData}
                        alt={"User Avatar Preview"}
                        className={"rounded-full h-52 w-52 object-cover border border-gray-800"}
                        aria-hidden="true"
                    />
                    :
                    <img
                        src={defaultAvatar}
                        alt={"Default Avatar"}
                        className={"rounded-full h-52 w-52 object-cover border border-gray-800"}
                        aria-hidden="true"
                    />
            }

            <div className={"flex space-x-5 mt-10"}>
                <button
                    disabled={readonly}
                    type={"button"}
                    onClick={clearPreview}
                    className={"border border-gray-700 rounded-md  px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"}
                >
                    Remove
                </button>

                <label
                    role={readonly ? "" : "button"}

                    className={resolveClassNames(
                        "rounded-md px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600",
                        readonly ? "bg-gray-600 hover:bg-gray-600" : "bg-indigo-600 hover:bg-indigo-500 text-white"
                    )}
                    htmlFor={"file-input"}>{imageIsSet ? "Change" : "Upload"}
                </label>

                <input
                    id={"file-input"}
                    disabled={readonly}
                    className={"rounded-md border border-gray-700 px-2.5 py-1.5 text-sm font-semibold shadow-sm ring-1 ring-inset ring-gray-300 hidden"}
                    type={"file"} accept={"image/png image/jpeg"}
                    onChange={(event) => previewAvatar(event)}
                />
            </div>
        </div>
    )
}