import React, {Fragment, useEffect, useState} from "react";
import MessageIndicator, {getDefaultMessageIndicatorInfo} from "../../Components/MessageIndicator";
import {appendImageOrigin, getCurrentCafMenu, setCafItemStatus} from "../../Helpers/APIHelpers";
import {CafMenu, DailyMenu} from "./CafMenuPage";
import PosTerminalIcon from "../../assets/images/pos-terminal-icon.svg";
import {resolveClassNames} from "../../Helpers/ApplicationHelpers";


export interface CafItem {
    cafItemId: string
    name: string
    displayName: string
    status: string
    imageUrl: string
}


function CafItemCard({cafItem, handleItemStatusChange}:{cafItem: CafItem, handleItemStatusChange: (cafItemId: string, event: React.ChangeEvent<HTMLSelectElement>) => void}) {

    return (
        <div className={"rounded-2xl border border-gray-800 overflow-hidden"}>
            <div className={"flex flex-col h-64 w-64 md:w-60"}>
                <div className={"h-2/3"}>
                    <img
                        src={cafItem.imageUrl}
                        alt={cafItem.name}
                        className={"w-full h-full object-cover"}
                    />
                </div>
                <div className={"h-1/3"}>
                    <div className={"h-3/6 px-2 flex flex-col justify-center items-start"}>
                        <div><span className={"font-semibold"}>{cafItem.displayName}</span></div>
                    </div>
                    <div className={"w-full flex justify-center items-center h-3/6 px-2 text-sm font-semibold text-white"}>
                        <select
                            id="status"
                            name="status"
                            className="bg-gray-900 block w-full rounded-md border-0 py-1.5 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            onChange={(event) => {handleItemStatusChange(cafItem.cafItemId, event)}}
                            defaultValue={cafItem.status}
                        >
                            <option value="Available">Available</option>
                            <option value="Temporarily Unavailable">Temporarily Unavailable</option>
                            <option value="Unavailable">Unavailable</option>
                        </select>
                    </div>
                </div>
            </div>
        </div>
    )
}


function CafMenuHolder({handleItemStatusChange, cafMenu}: {cafMenu: CafMenu, handleItemStatusChange: (cafItemId: string, event: React.ChangeEvent<HTMLSelectElement>) => void}) {

    return (
        <div className={""}>

            <div className={"flex flex-col justify-center space-y-2"}>
                <div className={"w-full font-semibold text-2xl"}><p>{cafMenu.period}</p></div>
            </div>

            <div>
                <div className={"p-5 py-10 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 mx-auto border rounded-2xl border-gray-700 mt-5 justify-items-center gap-y-10"}>
                    {cafMenu.items.map((cafItem, index) => (
                        <CafItemCard
                            cafItem={cafItem}
                            handleItemStatusChange={handleItemStatusChange}
                            key={index}
                        />))}
                </div>
            </div>
        </div>
    )
}

export default function EditCafeteriaItemsStatusPage() {
    const [dailyCafMenuId, setDailyCafMenuId] = useState("")
    const [cafMenu, setCafMenu] = useState<CafMenu>({
        cafMenuId: "",
        period: "",
        starts: Date.now(),
        ends:  Date.now(),
        items: []
    });
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo());

    useEffect(() => {
        getCurrentCafMenu().then((response) => {
            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                return
            }

            let currentTime = Date.now();

            let menu: CafMenu | undefined = response.data.menus.find((menu: CafMenu) => (currentTime >= menu.starts) && (currentTime <= menu.ends));

            // let menu: CafMenu | undefined = response.data.menus[0];

            if (!menu) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: "No cafeteria menu is currently active. Try again later"
                });
                return
            }

            setDailyCafMenuId(response.data.dailyCafMenuId);

            for (let item of menu.items) {
                item.imageUrl = appendImageOrigin(item.imageUrl);
            }

            setCafMenu(menu);

        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        })
    }, [])

    function handleItemStatusChange(cafItemId: string, event: React.ChangeEvent<HTMLSelectElement>) {
        const value = event.target.value;

        setCafItemStatus(
            dailyCafMenuId,
            cafMenu.cafMenuId,
            cafItemId,
            value
        ).then((response) => {
            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                return
            }

        }).catch((e) => {
            let err = e as Error

            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        })
    }


    return (
        <div>
            <div className="w-full sm:px-0">
                <div
                    className={resolveClassNames(
                        'rounded-xl p-3'
                    )}
                >
                    <CafMenuHolder
                        handleItemStatusChange={handleItemStatusChange}
                        cafMenu={cafMenu}
                    />

                </div>

            </div>

            <div className={"mt-5 rounded"}>
                <MessageIndicator
                    state={messageIndicatorInfo.state}
                    message={messageIndicatorInfo.message}
                />
            </div>
        </div>
    )
}