import React, {useState} from "react";
import MessageIndicator, {getDefaultMessageIndicatorInfo} from "../../Components/MessageIndicator";
import {PageProps} from "../../Components/Navigation/NavBar";
import {resolveClassNames} from "../../Helpers/ApplicationHelpers";
import {deleteDailyCafMenu} from "../../Helpers/APIHelpers";
import {createCafMenuPage} from "../../SessionManager";

export default function DeleteCafeteriaMenuPage({updateMainContent}: PageProps) {
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo);
    const [requestOngoing, setRequestOngoing] = useState(false);

    const deleteButtonIsActive = !requestOngoing

    function onDeleteMenuHandler() {
        setRequestOngoing(true)

        deleteDailyCafMenu().then((response) => {
            setMessageIndicatorInfo({
                state: response.success ? "success" : "error",
                message: response.message
            });

            if (!response.success) {
                setRequestOngoing(false);
                return
            }

        }).catch((e) => {
            let err = e as Error

            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });

            setRequestOngoing(false)
        })
    }

    return (
        <>
            <div className={"flex justify-center items-center"}>
                <div className={"w-full max-w-2xl flex  flex-col rounded-md border-gray-800 border"}>
                    <div className={"p-2"}>
                        <p className={"font-semibold text-red-500"}>All items in the current menu along with their ratings and reviews will be deleted. This action cannot be undone.</p>
                    </div>
                    <div className="p-2 mt-6 flex items-center justify-end gap-x-6 border-t border-gray-800">
                        <button
                            onClick={onDeleteMenuHandler}
                            disabled={!deleteButtonIsActive}
                            className={resolveClassNames(
                                "rounded-md  px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600",
                                deleteButtonIsActive ? "bg-red-500" : "bg-gray-600 hover:bg-gray-600"
                            )}
                        >
                            Delete Menu
                        </button>
                    </div>
                </div>
            </div>


            <div className={"mt-5"}>
                <MessageIndicator
                    state={messageIndicatorInfo.state}
                    message={messageIndicatorInfo.message}
                />
            </div>
        </>
    )
}