import {Disclosure} from "@headlessui/react";
import {AppInfo, sessionManager, signInPage, userProfilePage, UserSummary} from "../../SessionManager";
import {Page} from "./NavBar";
import {destroySession, switchToGuestSession} from "../../Helpers/ApplicationHelpers";

export default function MobileUserNav({isGuestSession, userSummary, updateMainContent, refreshApp}: {isGuestSession: boolean, userSummary: UserSummary, updateMainContent: (page: Page) => void, refreshApp: (appInfo: AppInfo) => void}) {

    function signOut() {
        destroySession().then(() => {
            switchToGuestSession().then(() => {
                refreshApp(sessionManager.getAppInfo());
            })
        })
    }

    return (
        <div className="border-t border-gray-700 pb-3 pt-4 px-2">
            <div className="flex items-center px-5">
                <div className="flex-shrink-0">
                    <img className="h-10 w-10 rounded-full" src={userSummary.imageUrl} alt="User Avatar"/>
                </div>
                <div className="ml-3 flex flex-col space-y-1.5">
                    <div
                        className="text-base font-medium leading-none">{`${userSummary.firstname} ${userSummary.lastname}`}</div>
                    <div
                        className="text-sm font-medium leading-none text-gray-400">{userSummary.email}</div>
                </div>
            </div>

            {isGuestSession ?
                <div className="mt-3 space-y-1 px-2">
                    <Disclosure.Button
                        onClick={() => updateMainContent(signInPage)}
                        key={"log-in"}
                        as="button"
                        className="w-full text-left block rounded-md px-3 py-2 text-base font-medium hover:bg-gray-800 hover:text-gray-100"
                    >
                        {"Log In"}
                    </Disclosure.Button>


                </div>

                :

                <div className="mt-3 space-y-1 px-2">
                    <Disclosure.Button
                        onClick={() => {updateMainContent(userProfilePage)}}
                        key={"profile"}
                        as={"button"}
                        className="w-full text-left block rounded-md px-3 py-2 text-base font-medium hover:bg-gray-800 hover:text-gray-100"
                    >
                        {"Your Profile"}
                    </Disclosure.Button>

                    <Disclosure.Button
                        onClick={signOut}
                        key={"sign-out"}
                        as={"button"}
                        className="w-full text-left block rounded-md px-3 py-2 text-base font-medium hover:bg-gray-800 hover:text-gray-100"
                    >
                        {"Sign Out"}
                    </Disclosure.Button>
                </div>
            }
        </div>
    )
}