export interface ModelPermission {
    modelName: string
    allowedMethods: string[]
}

export interface SessionSummary {
    sessionId: string
    isGuestSession: boolean
    userId: string
    groups: string[],
    accessRights: ModelPermission[]
}

class SessionManager {
    private constructor() {
    }

    private static instance = new SessionManager();

    static getInstance() {
        return this.instance
    }

    private sessionSummary: SessionSummary = structuredClone(defaultSessionSummary);
    private userSummary: UserSummary = structuredClone(defaultUserSummary);


    private setSessionSummary(sessionSummary: SessionSummary) {
        this.sessionSummary = sessionSummary
    }

    getSessionSummary() {
        return this.sessionSummary
    }


    getUserSummary() {
        return this.userSummary;
    }

    private setUserSummary(userInfo: UserSummary) {
        this.userSummary = userInfo;
    }

    activateApp(
        sessionId: string,
        isGuestSession: boolean,
        userId: string,
        groups: string[],
        accessRights: ModelPermission[],
        firstname: string,
        lastname: string,
        email: string,
        imageUrl: string
    ) {
        this.setSessionSummary({
            sessionId: sessionId,
            isGuestSession: isGuestSession,
            userId: userId,
            groups: groups,
            accessRights: accessRights
        });

        this.setUserSummary({
            firstname: firstname,
            lastname: lastname,
            email: email,
            imageUrl: imageUrl
        });

        this.generateUserNavigation();

        this.isSessionActive = true;
    }

    deactivateApp() {
        this.sessionSummary = defaultSessionSummary;
        this.userSummary = defaultUserSummary;
        this.userNavigation.length = 0;
        this.isSessionActive = false;
    }

    getAppInfo(): AppInfo {
        return {
            firstname: this.userSummary.firstname,
            lastname: this.userSummary.lastname,
            email: this.userSummary.email,
            imageUrl: this.userSummary.imageUrl,
            navigation: this.userNavigation,
            isGuestSession: this.sessionSummary.isGuestSession,
            isSessionActive: this.isSessionActive
        }
    }

    userNavigation: PageGroup[] = [];

    isSessionActive: boolean = false;

    applicationNavigationInfo: PageGroup[] = [
        cafeteriaPagesInfo,
        coffeeShopPagesInfo,
        customerPagesInfo,
        managementPagesInfo,
        administrationPagesInfo,
    ]

    generateUserNavigation() {
        let userNavigationInfo: PageGroup[] = [];

        // For each application PageGroup
         for (const pageGroup of this.applicationNavigationInfo) {

            // For each PageInfo in application PageGroup
             loop1: for (const page of pageGroup.pages) {

                // For each required access right of PageInfo
                for (const requiredAccessRight of page.requiredAccessRights) {
                    const correspondingUserAccessRight = this.sessionSummary.accessRights.find((accessRight) => (accessRight.modelName === requiredAccessRight.modelName));

                    if (!correspondingUserAccessRight) {
                        continue loop1
                    }


                    for (const requireMethod of requiredAccessRight.requiredMethods) {
                        if (!correspondingUserAccessRight.allowedMethods.includes(requireMethod)) {
                            continue loop1
                        } else {
                        }
                    }
                }

                const existingPageGroup = userNavigationInfo.find((potentialPageGroup) => (potentialPageGroup.groupName=== pageGroup.groupName));

                if (!existingPageGroup) {
                    userNavigationInfo.push({
                        groupName: pageGroup.groupName,
                        pages: [page]
                    })
                } else {
                    existingPageGroup.pages.push(page)
                }

            }
        }

        this.userNavigation.length = 0;
        this.userNavigation = userNavigationInfo;
    }
}

const sessionManager = SessionManager.getInstance();

export {
    sessionManager
}