import React, {Fragment, useEffect, useState} from 'react'
import {Dialog, Tab, Transition} from '@headlessui/react'
import {resolveClassNames} from "../../Helpers/ApplicationHelpers";
import {Rating} from '@mui/material';
import PosTerminalIcon from "../../assets/images/pos-terminal-icon.svg";
import MessageIndicator, {getDefaultMessageIndicatorInfo} from "../../Components/MessageIndicator";
import {appendImageOrigin, getCafPosStatus, getCurrentCafMenu, submitCafItemReview} from "../../Helpers/APIHelpers";
import {sessionManager} from "../../SessionManager";

export interface CafItemReview {
    reviewId: string
    rating: number
    reviewText: string
}

export interface CafItem {
    cafItemId: string
    name: string
    displayName: string
    description: string
    status: string
    type: string
    imageUrl: string
    noOfReviews: number
    averageRating: number
    reviews: CafItemReview[]
}

export interface CafMenu {
    cafMenuId: string
    period: string
    starts: number
    ends: number
    items: CafItem[]
}

export interface DailyMenu {
    dayTimestamp: number
    day: string
    menus: CafMenu[]
}

export interface CafItemReview {
    reviewId: string
    rating: number
    reviewText: string
    posterName: string
}

interface FormData {
    reviewText: string
    rating: number
}

function getDefaultFormData(): FormData {
    return {
        reviewText: "",
        rating: 0
    }
}


function ReviewBox({review}: { review: CafItemReview }) {
    return (
        <div className={"flex flex-col p-3 space-y-1.5 border-gray-800 border rounded-md"}>
            <div className={"font-semibold"}>
                {review.posterName}
            </div>
            <div className={"flex flex-col px-0.5 space-y-1.5"}>
                <div className={"text-sm"}>
                    {review.reviewText}
                </div>
                <div>
                    <Rating
                        value={review.rating}
                        size={"small"}
                        sx={{
                            "& .MuiRating-iconEmpty": {
                                color: "gray",
                            },
                        }}
                        readOnly
                    />
                </div>
            </div>
        </div>
    )
}

function CafItemReviewsDialog({dailyCafMenuId, cafMenuId, cafItemId, reviews, updateReviews}: {
    dailyCafMenuId: string,
    cafMenuId: string,
    cafItemId: string
    reviews: CafItemReview[]
    updateReviews: (newReview: CafItemReview, newRating: number) => void
}) {
    const [isOpen, setIsOpen] = useState(false)
    const [requestOngoing, setRequestOngoing] = useState(false);
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo());
    const [itemReviews, setItemReviews] = useState<CafItemReview[]>(reviews)
    const [formData, setFormData] = useState(getDefaultFormData());


    let inputFields = [
        formData.reviewText,
    ]

    let inputsAreValid = true;

    for (const inputField of inputFields) {
        if (inputField.length < 1) {
            inputsAreValid = false
            break
        }
    }

    let submitButtonIsActive = (inputsAreValid) && (!requestOngoing);

    function closeModal() {
        setMessageIndicatorInfo(getDefaultMessageIndicatorInfo)
        setFormData(getDefaultFormData)
        setIsOpen(false)
    }

    function openModal() {
        setIsOpen(true)
    }

    function handleReviewSubmit(event: React.FormEvent<HTMLFormElement>) {
        event.preventDefault();

        setRequestOngoing(true);

        const userSummary = sessionManager.getUserSummary();

        const rating = formData.rating;
        const reviewText = formData.reviewText;
        const posterName = `${userSummary.firstname} ${userSummary.lastname}`

        submitCafItemReview(
            dailyCafMenuId,
            cafMenuId,
            cafItemId,
            rating,
            reviewText,
            posterName
        ).then((response) => {

            setMessageIndicatorInfo({
                state: response.success ? "success" : "error",
                message: response.message
            });

            setRequestOngoing(false);

            if (!response.success) {
                return
            }

            updateReviews({
                reviewId: response.data.reviewId,
                rating: rating,
                reviewText: reviewText,
                posterName: posterName
            }, rating)

            setItemReviews([
                ...itemReviews,
                {
                    reviewId: response.data.reviewId,
                    rating: rating,
                    reviewText: reviewText,
                    posterName: posterName
                },
            ])

        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });

            setRequestOngoing(false);
            return
        })
    }

    function handleReviewChange(event: React.ChangeEvent<HTMLTextAreaElement>) {

        const {name, value} = event.target;

        let newFormData = {
            ...formData,
            reviewText: value
        }
        setFormData(newFormData);
    }

    function handleRatingChange(event: React.SyntheticEvent<Element, Event>, newValue: number | null) {

        if (!newValue) {
            return
        }

        let newFormData: FormData = {
            ...formData,
            rating: newValue
        }
        setFormData(newFormData);
    }

    return (
        <>
            <div className="inset-0 flex items-center justify-center overflow-hidden">
                <button
                    type="button"
                    onClick={openModal}
                    className={resolveClassNames(
                        "p-1.5 rounded text-xs font-semibold shadow-sm focus-visible:outline-indigo-600 bg-indigo-600 hover:bg-indigo-500 text-white"
                    )}
                >
                    Reviews
                </button>
            </div>

            <Transition.Root show={isOpen} as={Fragment}>
                <Dialog
                    as="div"
                    className="relative z-10"
                    onClose={closeModal}
                    open={isOpen}
                >
                    <Transition.Child
                        as={Fragment}
                        enter="ease-out duration-300"
                        enterFrom="opacity-0"
                        enterTo="opacity-100"
                        leave="ease-in duration-200"
                        leaveFrom="opacity-100"
                        leaveTo="opacity-0"
                    >
                        <div className="fixed inset-0 bg-gray-800 bg-opacity-75 transition-opacity"/>
                    </Transition.Child>

                    <div className="fixed inset-0 z-10 overflow-y-auto">
                        <div
                            className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
                            <Transition.Child
                                as={Fragment}
                                enter="ease-out duration-300"
                                enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                                enterTo="opacity-100 translate-y-0 sm:scale-100"
                                leave="ease-in duration-200"
                                leaveFrom="opacity-100 translate-y-0 sm:scale-100"
                                leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                            >
                                <Dialog.Panel
                                    className="relative transform overflow-hidden rounded-lg text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg">

                                    <div className={"bg-gray-900 px-4 pb-4 pt-5 sm:p-6 sm:pb-4 text-gray-200"}>
                                        <div>

                                            <form autoComplete={"off"} className={""} onSubmit={(event) => {
                                                handleReviewSubmit(event)
                                            }}>

                                                <div className={"font-semibold text-2xl"}>Item Reviews</div>

                                                <div className={"space-y-5 mt-2"}>

                                                    <div
                                                        className="py-3 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 gap-x-6 gap-y-8">

                                                        <div className="col-span-full">
                                                            <div className="mt-2">

                                                                <textarea
                                                                    id="reviesText"
                                                                    name="reviewText"
                                                                    rows={3}
                                                                    placeholder={"Enter your review here"}
                                                                    className="bg-gray-900 block w-full rounded-md border-0 py-1.5 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                    defaultValue={''}
                                                                    onChange={(event) => {
                                                                        handleReviewChange(event)
                                                                    }}
                                                                />
                                                            </div>
                                                        </div>

                                                        <div className="col-span-full">
                                                            <div className="mt-2">
                                                                <Rating
                                                                    onChange={(event, newValue) => {
                                                                        handleRatingChange(event, newValue);
                                                                    }}
                                                                    name={"rating"}
                                                                    value={formData.rating}
                                                                    sx={{
                                                                        "& .MuiRating-iconEmpty": {
                                                                            color: "gray",
                                                                        },
                                                                    }}
                                                                />
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div className=" flex items-center justify-end gap-x-6">
                                                    <button
                                                        type="submit"
                                                        disabled={!submitButtonIsActive}
                                                        className={resolveClassNames(
                                                            "rounded-md p-2 text-sm font-semibold shadow-sm focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600",
                                                            submitButtonIsActive ? "bg-indigo-600 hover:bg-indigo-500 text-white" : "bg-gray-600 hover:bg-gray-600"
                                                        )}
                                                    >
                                                        Submit Review
                                                    </button>
                                                </div>
                                                <div className={"mt-5 rounded"}>
                                                    <MessageIndicator
                                                        state={messageIndicatorInfo.state}
                                                        message={messageIndicatorInfo.message}
                                                    />
                                                </div>
                                            </form>
                                        </div>

                                        <div className={"flex flex-col space-y-1.5 mt-5"}>
                                            {itemReviews.map((review, index) => (
                                                <ReviewBox review={review} key={index}/>))}
                                        </div>
                                    </div>
                                </Dialog.Panel>
                            </Transition.Child>
                        </div>
                    </div>
                </Dialog>
            </Transition.Root>
        </>
    )
}

function CafItemCard({cafItem, isCardActive, cafMenuId, dailyCafMenuId}: {
    cafItem: CafItem,
    dailyCafMenuId: string,
    cafMenuId: string,
    isCardActive: boolean
}) {
    const [reviews, setReviews] = useState(cafItem.reviews);
    const [averageRating, setAverageRating] = useState(cafItem.averageRating);
    const [noOfReviews, setNoOfReviews] = useState(cafItem.noOfReviews);

    function updateReviews(newReview: CafItemReview, newRating: number) {
        const newNoOfReviews = noOfReviews + 1;
        const newAverageRating = ((averageRating * noOfReviews) + newRating)/newNoOfReviews;

        setNoOfReviews(newNoOfReviews);
        setAverageRating(newAverageRating);
        setReviews([
            ...reviews,
            newReview
        ])
    }

    let statusClass: string;

    switch (cafItem.status) {
        case "Available": {
            statusClass = "bg-green-500"
            break
        }

        case "Temporarily Unavailable" : {
            statusClass = "bg-yellow-500"
            break
        }

        case "Unavailable" : {
            statusClass = "bg-red-500"
            break
        }

        default : {
            statusClass = "bg-black"
        }
    }


    return (
        <div className={"rounded-2xl border border-gray-800 overflow-hidden"}>
            <div className={"flex flex-col h-64 w-64 md:w-60"}>
                <div className={"h-2/3"}>
                    <img
                        src={cafItem.imageUrl}
                        alt={cafItem.name}
                        className={"w-full h-full object-cover"}
                    />
                </div>
                <div className={"h-1/3 flex flex-col justify-center items-start"}>
                    <div className={"h-4/6 px-2 flex justify-between w-full items-center"}>
                        <div className={"space-y-1.5 flex-col"}>
                            <div className={"font-semibold text-base"}>{cafItem.displayName}</div>
                            <div className={"flex text-sm"}>
                                <Rating
                                    name={cafItem.name}
                                    value={averageRating}
                                    size={"small"}
                                    readOnly
                                    precision={0.5}
                                    sx={{
                                        "& .MuiRating-iconEmpty": {
                                            color: "gray",
                                        },
                                    }}
                                />

                                <div className={"w-full text-center"}>
                                    ({noOfReviews})
                                </div>
                            </div>
                        </div>
                        {
                            isCardActive &&
                            <CafItemReviewsDialog
                                cafItemId={cafItem.cafItemId}
                                cafMenuId={cafMenuId}
                                dailyCafMenuId={dailyCafMenuId}
                                reviews={reviews}
                                updateReviews={updateReviews}
                            />
                        }
                    </div>
                    {

                        isCardActive &&
                        <div
                            className={`w-full flex justify-center items-center h-2/6 px-2 text-sm font-semibold text-white ${statusClass}`}>
                            {cafItem.status}
                        </div>
                    }
                </div>
            </div>
        </div>
    )
}

function CafMenuHolder({dailyCafMenuId, cafMenu}: { dailyCafMenuId: string, cafMenu: CafMenu }) {
    let mains: CafItem[] = cafMenu.items.filter((cafItem) => (cafItem.type === "main"));
    let sides: CafItem[] = cafMenu.items.filter((cafItem) => (cafItem.type === "side"));
    let proteins: CafItem[] = cafMenu.items.filter((cafItem) => (cafItem.type === "protein"));

    const currentTimestamp = Date.now()

    const isMenuActive = (currentTimestamp >= cafMenu.starts) && (currentTimestamp <= cafMenu.ends);

    return (
        <div className={"mt-5"}>

            <div className={"flex flex-col justify-center space-y-2"}>
                <div className={"w-full font-semibold text-2xl"}><p>{cafMenu.period}</p></div>
                <div className={"w-full font-semibold text-lg"}>
                    <p>{new Date(cafMenu.starts).toLocaleTimeString()} - {new Date(cafMenu.ends).toLocaleTimeString()}</p>
                </div>
            </div>

            <div>
                <h2 className={"text-2xl font-bold tracking-tight mt-10"}>Mains</h2>
                {mains.length > 0 ?
                    <div
                        className={"py-10 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 mx-auto border rounded-2xl border-gray-700 p-5 mt-5 justify-items-center gap-y-10"}>
                        {mains.map((cafItem, index) => (
                            <CafItemCard
                                cafItem={cafItem}
                                isCardActive={isMenuActive}
                                cafMenuId={cafMenu.cafMenuId}
                                dailyCafMenuId={dailyCafMenuId}
                                key={index}
                            />))}
                    </div>

                    :

                    <div className={"p-5"}>There are no items in this section</div>
                }
            </div>

            <div>
                <h2 className={"text-2xl font-bold tracking-tight mt-10"}>Sides</h2>
                {sides.length > 0 ?
                    <div
                        className={" py-10 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 mx-auto border rounded-2xl border-gray-700 p-5 mt-5 justify-items-center gap-y-10"}>
                        {sides.map((cafItem, index) => (
                            <CafItemCard
                                cafItem={cafItem}
                                isCardActive={isMenuActive}
                                cafMenuId={cafMenu.cafMenuId}
                                dailyCafMenuId={dailyCafMenuId}
                                key={index}
                            />))}
                    </div>

                    :

                    <div className={"p-5"}>There are no items in this section</div>
                }

            </div>

            <div>
                <h2 className={"text-2xl font-bold tracking-tight mt-10"}>Proteins</h2>
                {proteins.length > 0 ?
                    <div
                        className={" py-10 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 mx-auto border rounded-2xl border-gray-700 p-5 mt-5 justify-items-center gap-y-10"}>
                        {proteins.map((cafItem, index) => (
                            <CafItemCard
                                cafItem={cafItem}
                                isCardActive={isMenuActive}
                                cafMenuId={cafMenu.cafMenuId}
                                dailyCafMenuId={dailyCafMenuId}
                                key={index}
                            />))}
                    </div>

                    :

                    <div className={"p-5"}>There are no items in this section</div>
                }
            </div>
        </div>
    )
}

export default function CafMenuPage() {
    const [currentMenu, setCurrentMenu] = useState<DailyMenu>({
        day: "",
        dayTimestamp: Date.now(),
        menus: []
    });
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo());
    const [cafPosIsActive, setCafPosIsActive] = useState(false);
    const [dailyCafMenuId, setDailyCafMenuId] = useState("")

    useEffect(() => {
        getCafPosStatus().then((response) => {
            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                return
            }

            setCafPosIsActive(response.data.isActive);
        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        })
        getCurrentCafMenu().then((response) => {
            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                return
            }

            setDailyCafMenuId(response.data.dailyCafMenuId);

            const serverMenu: DailyMenu = response.data
            let menu: DailyMenu = structuredClone(serverMenu);

            for (let cafMenu of menu.menus) {
                for (let item of cafMenu.items) {
                    item.imageUrl = appendImageOrigin(item.imageUrl);
                }
            }

            setCurrentMenu(menu);

        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        })
    }, [])

    return (
        <div>
            <div className={"flex flex-col space-y-3"}>
                <div className={""}>
                    <img
                        src={PosTerminalIcon}
                        className={"h-24"}
                        alt={"Pos Terminal Icon"}
                    />
                </div>

                <div className={"flex flex-col space-y-0.5"}>
                    <p className={"font-semibold"}>Cafeteria POS Status</p>

                    <div className={"flex"}>
                        <p className={"font-semibold"}>{cafPosIsActive ? "Active" : "Inactive"}</p>
                        <div className={"flex justify-center items-center ml-3"}>
                            <div className={resolveClassNames(
                                "w-3 h-3 rounded-full",
                                cafPosIsActive ? "bg-green-500" : "bg-red-500"
                            )}></div>
                        </div>
                    </div>
                </div>
            </div>

            <div>
                <h2 className={"text-2xl font-bold tracking-tight mt-10"}>{currentMenu.day}</h2>
            </div>

            <div className="w-full sm:px-0 mt-10">
                <Tab.Group>
                    <Tab.List className="flex space-x-1 rounded-xl p-1 bg-gray-800">
                        {currentMenu.menus.map((cafMenu, index) => (
                            <Tab
                                key={index}
                                className={({selected}) =>
                                    resolveClassNames(
                                        'w-full rounded-lg py-2.5 text-sm font-medium leading-5',

                                        selected
                                            ? 'bg-gray-700'
                                            : 'text-blue-100 hover:bg-white/[0.12] hover:text-white'
                                    )
                                }
                            >
                                {cafMenu.period}
                            </Tab>
                        ))}
                    </Tab.List>
                    <Tab.Panels className="mt-2">
                        {currentMenu.menus.map((cafMenu, idx) => (
                            <Tab.Panel
                                key={idx}
                                className={resolveClassNames(
                                    'rounded-xl p-3'
                                )}
                            >
                                <CafMenuHolder
                                    cafMenu={cafMenu}
                                    dailyCafMenuId={dailyCafMenuId}
                                />
                            </Tab.Panel>
                        ))}
                    </Tab.Panels>
                </Tab.Group>
            </div>
            <div className={"mt-5 rounded"}>
                <MessageIndicator
                    state={messageIndicatorInfo.state}
                    message={messageIndicatorInfo.message}
                />
            </div>
        </div>
    )
}
