import MessageIndicator, {getDefaultMessageIndicatorInfo} from "../../Components/MessageIndicator";
import React, {useEffect, useState} from "react";
import {getCoffeeShopPosStatus, setCoffeeShopPosStatus} from "../../Helpers/APIHelpers";
import PosTerminalIcon from "../../assets/images/pos-terminal-icon.svg";

export default function EditCoffeeShopPosStatusPage() {
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo());
    const [coffeeShopPosIsActive, setCoffeeShopPosIsActive] = useState(false);

    useEffect(() => {
        getCoffeeShopPosStatus().then((response) => {
            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                return
            }

            setCoffeeShopPosIsActive(response.data.isActive);

        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        })
    }, [])

    function handlePosStatusChange(event:  React.ChangeEvent<HTMLSelectElement>) {
        const value = event.target.value
        const isActive = value === "active"

        setCoffeeShopPosStatus(isActive).then((response) => {

            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                setCoffeeShopPosIsActive(!isActive)

                return
            }

            setCoffeeShopPosIsActive(isActive)

        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });

            setCoffeeShopPosIsActive(!isActive)
        })
    }

    return (
        <div>
            <div className={"flex justify-center items-center"}>
                <div className={"w-fit flex flex-col space-y-2"}>
                    <div className={""}>
                        <img
                            src={PosTerminalIcon}
                            className={"h-36 w-36"}
                            alt={"Pos Terminal Icon"}
                        />
                    </div>
                    <div className={"w-full flex justify-center items-center h-3/6 px-2 text-sm font-semibold text-white"}>
                        <select
                            id="pos-status"
                            name="pos-status"
                            className="bg-gray-900 block w-full rounded-md border-0 py-1.5 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            onChange={(event) => {handlePosStatusChange(event)}}
                            value={coffeeShopPosIsActive ? "active" : "inactive"}
                        >
                            <option value="active">Active</option>
                            <option value="inactive">Inactive</option>
                        </select>
                    </div>
                </div>
            </div>

            <div className={"mt-5 rounded"}>
                <MessageIndicator
                    state={messageIndicatorInfo.state}
                    message={messageIndicatorInfo.message}
                />
            </div>
        </div>
    )
}