import {resolveClassNames} from "../../Helpers/ApplicationHelpers";
import React, {Fragment, useEffect, useState} from 'react'
import {Dialog, Transition} from '@headlessui/react'
import ImageSelector, {getDefaultImageInfo, ImageInfo} from "../../Components/ImageSelector";
import MessageIndicator, {getDefaultMessageIndicatorInfo} from "../../Components/MessageIndicator";
import {
    appendImageOrigin,
    createCafWarehouseItem,
    deleteCafWarehouseItem,
    getCafWarehouseItems
} from "../../Helpers/APIHelpers";
import {TrashIcon} from "@heroicons/react/24/outline";


export interface CafWarehouseItem {
    name: string
    displayName: string
    description: string
    type: string
    imageUrl: string
    cafWarehouseItemId: string
}

interface FormData {
    name: string
    displayName: string
    description: string
    type: string
}

function getDefaultFormData(): FormData {
    return {
        name: "",
        displayName: "",
        description: "",
        type: "main",
    }
}

function CreateWarehouseItemDialog({updateCafWarehouseItems}: {updateCafWarehouseItems: (newCafWarehouseItem: CafWarehouseItem) => void}) {
    const [isOpen, setIsOpen] = useState(false)
    const [formData, setFormData] = useState(getDefaultFormData());
    const [imageInfo, setImageInfo] = useState(getDefaultImageInfo());
    const [requestOngoing, setRequestOngoing] = useState(false);
    const [formIsReadonly, setFormIsReadonly] = useState(false);
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo());

    let inputFields = [
        formData.name,
        formData.description
    ]

    let inputsAreValid = true;

    for (const inputField of inputFields) {
        if (inputField.length < 1) {
            inputsAreValid = false
            break
        }
    }

    let submitButtonIsActive = (inputsAreValid) && (!requestOngoing) && (imageInfo.imageIsSet) && (!formIsReadonly);

    function handleInputChange(event: React.ChangeEvent<HTMLInputElement>) {
        const {name, value} = event.target;

        let newFormData = {
            ...formData,
            [name]: value
        }
        setFormData(newFormData);
    }

    function handleTextAreaChange(event: React.ChangeEvent<HTMLTextAreaElement>) {
        const {name, value} = event.target;

        let newFormData = {
            ...formData,
            [name]: value
        }
        setFormData(newFormData);
    }

    function handleTypeChange(event: React.ChangeEvent<HTMLSelectElement>) {
        const {name, value} = event.target;


        let newFormData = {
            ...formData,
            [name]: value
        }
        setFormData(newFormData);
    }

    function updateImagePreview(newImageInfo: ImageInfo) {
        setImageInfo(newImageInfo)
    }

    async function handleSubmit(event: React.FormEvent<HTMLFormElement>) {
        event.preventDefault();

        setMessageIndicatorInfo(getDefaultMessageIndicatorInfo);
        setRequestOngoing(true);
        setFormIsReadonly(true)

        createCafWarehouseItem(formData.name, formData.displayName, formData.description, formData.type, imageInfo.imageData).then((response) => {
            setRequestOngoing(false);
            setFormIsReadonly(false);

            setMessageIndicatorInfo({
                state: response.success ? "success" : "error",
                message: response.message
            })

            if (!response.success) {
                return
            }

            let item = response.data

            updateCafWarehouseItems({
                name: item.name,
                displayName: item.displayName,
                description: item.description,
                type: item.type,
                imageUrl: appendImageOrigin(item.imageUrl),
                cafWarehouseItemId: item.cafWarehouseItemId
            })

        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });

            setRequestOngoing(false);
            setFormIsReadonly(false);
            return
        })
    }

    function clearInputFields() {
        setFormData(getDefaultFormData());
        setImageInfo(getDefaultImageInfo());
        setRequestOngoing(false);
        setFormIsReadonly(false);
        setMessageIndicatorInfo(getDefaultMessageIndicatorInfo())
    }

    function closeModal() {
        clearInputFields();
        setIsOpen(false)
    }

    function openModal() {
        setIsOpen(true)
    }

    return (
        <>
            <div className="inset-0 flex items-center justify-center">
                <button
                    type="button"
                    onClick={openModal}
                    className={resolveClassNames(
                        "rounded-md  px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600 bg-indigo-600 hover:bg-indigo-500 text-white"
                    )}
                >
                    Add Item
                </button>
            </div>

            <Transition.Root show={isOpen} as={Fragment}>
                <Dialog
                    as="div"
                    className="relative z-10"
                    onClose={closeModal}
                    open={isOpen}
                >
                    <Transition.Child
                        as={Fragment}
                        enter="ease-out duration-300"
                        enterFrom="opacity-0"
                        enterTo="opacity-100"
                        leave="ease-in duration-200"
                        leaveFrom="opacity-100"
                        leaveTo="opacity-0"
                    >
                        <div className="fixed inset-0 bg-gray-800 bg-opacity-75 transition-opacity"/>
                    </Transition.Child>

                    <div className="fixed inset-0 z-10 overflow-y-auto">
                        <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
                            <Transition.Child
                                as={Fragment}
                                enter="ease-out duration-300"
                                enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                                enterTo="opacity-100 translate-y-0 sm:scale-100"
                                leave="ease-in duration-200"
                                leaveFrom="opacity-100 translate-y-0 sm:scale-100"
                                leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                            >
                                <Dialog.Panel
                                    className="relative transform overflow-hidden rounded-lg text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg">

                                    <div className={"bg-gray-900 px-4 pb-4 pt-5 sm:p-6 sm:pb-4 text-gray-200"}>
                                        <form autoComplete={"off"} className={""} onSubmit={handleSubmit}>

                                            <div className={"font-semibold text-2xl"}>Create Cafeteria Warehouse Item</div>

                                            <div className={"space-y-5 mt-5"}>

                                                <div className={""}>
                                                    <label htmlFor="item-image"
                                                           className="block text-sm font-medium leading-6">
                                                        Item Image
                                                    </label>
                                                    <div className={"mt-2"} id={"item-image"}>
                                                        <ImageSelector
                                                            imageIsSet={imageInfo.imageIsSet}
                                                            imageData={imageInfo.imageData}
                                                            updatePreview={updateImagePreview}
                                                            readonly={formIsReadonly}
                                                        />
                                                    </div>
                                                </div>


                                                <div
                                                    className="py-3 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 gap-x-6 gap-y-8">

                                                    <div className={""}>
                                                        <label htmlFor="name"
                                                               className="block text-sm font-medium leading-6">
                                                            Item Name
                                                        </label>
                                                        <div className="mt-2">
                                                            <div
                                                                className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600 px-2">

                                                                <input
                                                                    type="text"
                                                                    name="name"
                                                                    id="name"
                                                                    value={formData.name}
                                                                    className="block flex-1 border-0 bg-transparent py-1.5 pl-1 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6"
                                                                    maxLength={60}
                                                                    onChange={(event) => handleInputChange(event)}
                                                                    disabled={formIsReadonly}
                                                                    required={true}
                                                                />
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div className={""}>
                                                        <label htmlFor="displayName"
                                                               className="block text-sm font-medium leading-6">
                                                            Item Display Name
                                                        </label>
                                                        <div className="mt-2">
                                                            <div
                                                                className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600 px-2">

                                                                <input
                                                                    type="text"
                                                                    name="displayName"
                                                                    id="displayName"
                                                                    value={formData.displayName}
                                                                    className="block flex-1 border-0 bg-transparent py-1.5 pl-1 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6"
                                                                    maxLength={60}
                                                                    onChange={(event) => handleInputChange(event)}
                                                                    disabled={formIsReadonly}
                                                                    required={true}
                                                                />
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div className="">
                                                        <label htmlFor="type"
                                                               className="block text-sm font-medium leading-6">
                                                            Item Type
                                                        </label>

                                                        <div className="mt-2">

                                                            <select
                                                                id="type"
                                                                name="type"
                                                                className="bg-gray-900 block w-full rounded-md border-0 py-1.5 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                onChange={(event) => {
                                                                    handleTypeChange(event)
                                                                }}
                                                                defaultValue={"main"}
                                                            >
                                                                <option value="main">Main</option>
                                                                <option value="side">Side</option>
                                                                <option value="protein">Protein</option>
                                                            </select>
                                                        </div>
                                                    </div>

                                                    <div className="col-span-full">
                                                        <label htmlFor="description"
                                                               className="block text-sm font-medium leading-6">
                                                            Item Description
                                                        </label>

                                                        <div className="mt-2">

                                                    <textarea
                                                        id="description"
                                                        name="description"
                                                        rows={3}
                                                        className="bg-gray-900 block w-full rounded-md border-0 py-1.5 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                        defaultValue={''}
                                                        onChange={(event) => {
                                                            handleTextAreaChange(event)
                                                        }}
                                                    />
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>


                                            <div className={"mt-5 rounded"}>
                                                <MessageIndicator
                                                    state={messageIndicatorInfo.state}
                                                    message={messageIndicatorInfo.message}
                                                />
                                            </div>

                                            <div className="mt-6 flex items-center justify-end gap-x-6">
                                                <button
                                                    type={"button"}
                                                    className={resolveClassNames(
                                                        "border border-gray-700 rounded-md  px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600",
                                                    )}
                                                    onClick={() => {
                                                        closeModal()
                                                    }}
                                                >
                                                    Cancel
                                                </button>
                                                <button
                                                    type="submit"
                                                    disabled={!submitButtonIsActive}
                                                    className={resolveClassNames(
                                                        "rounded-md  px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600",
                                                        submitButtonIsActive ? "bg-indigo-600 hover:bg-indigo-500 text-white" : "bg-gray-600 hover:bg-gray-600"
                                                    )}
                                                >
                                                    Save Item
                                                </button>
                                            </div>
                                        </form>
                                    </div>
                                </Dialog.Panel>
                            </Transition.Child>
                        </div>
                    </div>
                </Dialog>
            </Transition.Root>
        </>
    )
}


export default function ManageCafWarehouseItemsPage() {
    const [cafWarehouseItems, setCafWarehouseItems] = useState<CafWarehouseItem[]>([]);
    const mains = cafWarehouseItems.filter((cafItem) => (cafItem.type === "main"))
    const sides = cafWarehouseItems.filter((cafItem) => (cafItem.type === "side"))
    const proteins = cafWarehouseItems.filter((cafItem) => (cafItem.type === "protein"))
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo());

    function updateCafWarehouseItemsOnScreen(newCafWarehouseItem: CafWarehouseItem) {
        setCafWarehouseItems([
            ...cafWarehouseItems,
            newCafWarehouseItem
        ])
    }

    function deleteItem(cafWarehouseItemId: string) {
        if (!window.confirm("Are you sure you want to delete this item?")) {
            return
        }
        deleteCafWarehouseItem(cafWarehouseItemId).then((response) => {
            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                return
            }

            let newCafWarehouseItems = cafWarehouseItems.filter((item) => item.cafWarehouseItemId !== cafWarehouseItemId)

            setCafWarehouseItems(newCafWarehouseItems);

        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        })
    }

    useEffect(() => {
        getCafWarehouseItems().then((response) => {

            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                return
            }

            let serverItems = response.data as CafWarehouseItem[];

            let cafWarehouseItems: CafWarehouseItem[] = serverItems.map((item) => {
                return {
                    ...item,
                    imageUrl: appendImageOrigin(item.imageUrl)
                }
            })

            setCafWarehouseItems(cafWarehouseItems)


        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        })
    }, [])


    return (
        <div>
            <div className={"mt-5 border-b border-gray-800"}>
                <div>
                    <h2 className={"text-2xl font-bold tracking-tight mt-10"}>Mains</h2>
                    {mains.length > 0 ?
                        <CafWarehouseItemsHolder cafWarehouseItems={mains} deleteCafWarehouseItem={deleteItem}/>
                        :
                        <NoCafWarehouseItemInSectionMessage />
                    }
                </div>

                <div>
                    <h2 className={"text-2xl font-bold tracking-tight mt-10"}>Sides</h2>
                    {sides.length > 0 ?
                        <CafWarehouseItemsHolder cafWarehouseItems={sides} deleteCafWarehouseItem={deleteItem} />
                        :
                        <NoCafWarehouseItemInSectionMessage />
                    }

                </div>

                <div>
                    <h2 className={"text-2xl font-bold tracking-tight mt-10"}>Proteins</h2>
                    {proteins.length > 0 ?
                        <CafWarehouseItemsHolder cafWarehouseItems={proteins} deleteCafWarehouseItem={deleteItem}/>
                        :
                        <NoCafWarehouseItemInSectionMessage />
                    }
                </div>
            </div>

            <div className={"mt-5 rounded"}>
                <MessageIndicator
                    state={messageIndicatorInfo.state}
                    message={messageIndicatorInfo.message}
                />
            </div>

            <div className="mt-6 flex items-center justify-end gap-x-6">
                <CreateWarehouseItemDialog updateCafWarehouseItems={updateCafWarehouseItemsOnScreen}/>
            </div>

        </div>
    )
}

function CafWarehouseItemCard({cafWarehouseItem, deleteCafWarehouseItem}: {cafWarehouseItem: CafWarehouseItem, deleteCafWarehouseItem: (itemId: string) => void}) {

    return (
        <div className={"rounded-2xl border border-gray-800 overflow-hidden"}>
            <div className={"flex flex-col h-64 w-64 md:w-60"}>
                <div className={"h-2/3"}>
                    <img
                        src={cafWarehouseItem.imageUrl}
                        alt={cafWarehouseItem.name}
                        className={"w-full h-full object-cover"}
                    />
                </div>
                <div className={"flex justify-between items-center space-y-2 h-1/3 px-2"}>
                    <div className={"flex flex-col space-y-2"}>
                        <div className={"text-base font-semibold"}>{cafWarehouseItem.displayName}</div>
                        <div className={"text-xs font-semibold"}>{cafWarehouseItem.name}</div>
                    </div>

                    <button
                        className={"h-8 w-8 bg-gray-800 rounded-md flex justify-center items-center"}
                        onClick={() => {deleteCafWarehouseItem(cafWarehouseItem.cafWarehouseItemId)}}
                    >
                        <TrashIcon className={"h-6 w-6 text-red-500"}/>
                    </button>
                </div>
            </div>
        </div>
    )
}

function CafWarehouseItemsHolder({cafWarehouseItems, deleteCafWarehouseItem}: {cafWarehouseItems: CafWarehouseItem[], deleteCafWarehouseItem: (itemId: string) => void}) {
    return (
        <>
            <div
                className={"grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 mx-auto border rounded-2xl border-gray-700 p-5 mt-5 justify-items-center gap-y-10"}
            >
                {cafWarehouseItems.map((item, index) => (
                    <CafWarehouseItemCard
                        deleteCafWarehouseItem={deleteCafWarehouseItem}
                        cafWarehouseItem={item}
                        key={index}
                    />))}
            </div>
        </>
    )
}

function NoCafWarehouseItemInSectionMessage() {
    return (
        <div className={"p-5"}>There are no items in this section</div>
    )
}