import {Tab} from "@headlessui/react";
import {resolveClassNames} from "../../Helpers/ApplicationHelpers";
import MessageIndicator, {getDefaultMessageIndicatorInfo} from "../../Components/MessageIndicator";
import React, {useEffect, useState} from "react";
import {CafWarehouseItem} from "./ManageCafWarehouseItemsPage";
import {appendImageOrigin, createDailyCafMenu, getCafWarehouseItems} from "../../Helpers/APIHelpers";

const breakfastStartTime = new Date().setHours(0, 0, 0);
const breakfastEndTime = new Date().setHours(7, 59, 59);
const lunchStartTime = new Date().setHours(8, 0, 0);
const lunchEndTime = new Date().setHours(15, 59, 59);
const dinnerStartTime = new Date().setHours(16, 0, 0);
const dinnerEndTime = new Date().setHours(23, 59, 59);

export interface MenuItemInfo {
    name: string
    displayName: string
    type: string
    imageUrl: string
    cafWarehouseItemId: string
    selected: boolean
}

export interface CafMenuCreationInfo {
    period: string
    starts: number
    ends: number
    cafWarehouseItemIds: string[]
}


function generateCafMenuCreationInfo (
    period: string,
    starts: number,
    ends: number,
    menuItemsInfo: MenuItemInfo[]
): CafMenuCreationInfo {
    let selectedItemIds: string[] = [];

    for (const item of menuItemsInfo) {
        if (item.selected) {
            selectedItemIds.push(item.cafWarehouseItemId);
        }
    }

    return {
        period: period,
        starts: starts,
        ends: ends,
        cafWarehouseItemIds: selectedItemIds
    }
}


function MenuItemCard({cardInfo, handleBoxChange, isReadonly}: {
    cardInfo: MenuItemInfo,
    handleBoxChange: (event: React.ChangeEvent<HTMLInputElement>) => void
    isReadonly: boolean
}) {

    return (
        <div className={"rounded-2xl border border-gray-800 overflow-hidden"}>
            <div className={"flex flex-col h-64 w-60"}>
                <div className={"h-2/3"}>
                    <img
                        src={cardInfo.imageUrl}
                        alt={cardInfo.name}
                        className={"w-full h-full object-cover"}
                    />
                </div>
                <div className={"flex justify-between items-center space-y-2 h-1/3 px-2"}>
                    <div className={"flex flex-col space-y-2"}>
                        <div className={"text-base font-semibold"}>{cardInfo.displayName}</div>
                        <div className={"text-xs font-semibold"}>{cardInfo.name}</div>
                    </div>

                    <input
                        type={"checkbox"}
                        id={cardInfo.name}
                        name={cardInfo.name}
                        checked={cardInfo.selected}
                        disabled={isReadonly}
                        onChange={(event) => {
                            handleBoxChange(event)
                        }}
                        className={"h-7 w-7 bg-gray-800 rounded flex justify-center items-center"}
                    >
                    </input>
                </div>
            </div>
        </div>
    )
}

function MenuCardHolder({menuItemsInformation, handleBoxChange, isReadonly}: {
    menuItemsInformation: MenuItemInfo[],
    handleBoxChange: (event: React.ChangeEvent<HTMLInputElement>) => void
    isReadonly: boolean
}) {

    return (
        <>
            <div
                className={"grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 mx-auto border rounded-2xl border-gray-700 p-5 mt-5 justify-items-center gap-y-10"}
            >
                {menuItemsInformation.map((cardInfo, index) => (
                    <MenuItemCard
                        cardInfo={cardInfo}
                        handleBoxChange={handleBoxChange}
                        key={index}
                        isReadonly={isReadonly}
                    />))}
            </div>
        </>
    )
}


function MenuPeriodBox({period, startTime, endTime, periodItemsInformation, updatePeriodItemsInformation, isReadonly}: {
    period: string,
    startTime: number,
    endTime: number,
    periodItemsInformation: MenuItemInfo[]
    updatePeriodItemsInformation: (menuItemsInfos: MenuItemInfo[]) => void
    isReadonly: boolean
}) {
    const mains = periodItemsInformation.filter((itemInfo) => (itemInfo.type === "main"))
    const sides = periodItemsInformation.filter((itemInfo) => (itemInfo.type === "side"))
    const proteins = periodItemsInformation.filter((itemInfo) => (itemInfo.type === "protein"))


    function handleBoxChange(event: React.ChangeEvent<HTMLInputElement>) {
        const {name, checked} = event.target

        let newMenuItemsInformation = periodItemsInformation.map((itemInfo) => {

            if (itemInfo.name === name) {
                return {
                    ...itemInfo,
                    selected: checked
                }
            }

            return itemInfo
        })

        updatePeriodItemsInformation(newMenuItemsInformation);
    }

    return (
        <>
            <div className={"flex flex-col justify-center space-y-2"}>
                <div className={"w-full font-semibold text-2xl"}><p>{period}</p></div>
                <div className={"w-full font-semibold text-lg"}><p>{new Date(startTime).toLocaleTimeString()} - {new Date(endTime).toLocaleTimeString()}</p></div>
            </div>

            <div className={"mt-5 border-b border-gray-800"}>
                <div>
                    <h2 className={"text-2xl font-bold tracking-tight mt-10"}>Mains</h2>
                    {mains.length > 0 ?
                        <MenuCardHolder menuItemsInformation={mains} handleBoxChange={handleBoxChange}
                                        isReadonly={isReadonly}/>
                        :
                        <NoItemInSectionMessage/>
                    }
                </div>

                <div>
                    <h2 className={"text-2xl font-bold tracking-tight mt-10"}>Sides</h2>
                    {sides.length > 0 ?
                        <MenuCardHolder menuItemsInformation={sides} handleBoxChange={handleBoxChange}
                                        isReadonly={isReadonly}/>
                        :
                        <NoItemInSectionMessage/>
                    }

                </div>

                <div>
                    <h2 className={"text-2xl font-bold tracking-tight mt-10"}>Proteins</h2>
                    {proteins.length > 0 ?
                        <MenuCardHolder menuItemsInformation={proteins} handleBoxChange={handleBoxChange}
                                        isReadonly={isReadonly}/>
                        :
                        <NoItemInSectionMessage/>
                    }
                </div>
            </div>
        </>
    )
}

export default function CreateCafeteriaMenuPage() {
    const [breakfastItemsInfo, setBreakfastItemsInfo] = useState<MenuItemInfo[]>([])
    const [lunchItemsInfo, setLunchItemsInfo] = useState<MenuItemInfo[]>([])
    const [dinnerItemsInfo, setDinnerItemsInfo] = useState<MenuItemInfo[]>([])
    const [messageIndicatorInfo, setMessageIndicatorInfo] = useState(getDefaultMessageIndicatorInfo());
    const [requestOngoing, setRequestOngoing] = useState(false);
    const [isReadonly, setIsReadonly] = useState(false)


    let breakfastCheckboxesAreValid = false
    let lunchCheckboxesAreValid = false
    let dinnerCheckboxesAreValid = false

    for (const itemInfo of breakfastItemsInfo) {
        if (itemInfo.selected) {
            breakfastCheckboxesAreValid = true
            break
        }
    }

    for (const itemInfo of lunchItemsInfo) {
        if (itemInfo.selected) {
            lunchCheckboxesAreValid = true
            break
        }
    }

    for (const itemInfo of dinnerItemsInfo) {
        if (itemInfo.selected) {
            dinnerCheckboxesAreValid = true
            break
        }
    }

    function onMenuSave() {
        const breakfastMenuCreationInfo = generateCafMenuCreationInfo(
            "Breakfast",
            breakfastStartTime,
            breakfastEndTime,
            breakfastItemsInfo
        )

        const lunchMenuCreationInfo = generateCafMenuCreationInfo(
            "Lunch",
            lunchStartTime,
            lunchEndTime,
            lunchItemsInfo
        )

        const dinnerMenuCreationInfo = generateCafMenuCreationInfo(
            "Dinner",
            dinnerStartTime,
            dinnerEndTime,
            dinnerItemsInfo
        )

        const dailyMenuCreationInfo = [
            breakfastMenuCreationInfo,
            lunchMenuCreationInfo,
            dinnerMenuCreationInfo
        ]

        setRequestOngoing(true)
        setIsReadonly(true)

        createDailyCafMenu(dailyMenuCreationInfo).then((response) => {

            setRequestOngoing(false)

            setMessageIndicatorInfo({
                state: response.success ? "success" : "error",
                message: response.message
            });

            if (!response.success) {
                setIsReadonly(false)
                return
            }



        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });

            setRequestOngoing(false)
            setIsReadonly(false)

            return
        })
    }

    let checkBoxesAreValid = breakfastCheckboxesAreValid && lunchCheckboxesAreValid && dinnerCheckboxesAreValid;

    let submitButtonIsActive = (checkBoxesAreValid) && (!isReadonly) && (!requestOngoing);


    function updateBreakfastItemsInformation(breakfastItemsInfo: MenuItemInfo[]) {
        setBreakfastItemsInfo(breakfastItemsInfo);
    }

    function updateLunchItemsInformation(lunchItemsInfo: MenuItemInfo[]) {
        setLunchItemsInfo(lunchItemsInfo);
    }

    function updateDinnerInformation(dinnerItemsInfo: MenuItemInfo[]) {
        setDinnerItemsInfo(dinnerItemsInfo);
    }

    useEffect(() => {
        getCafWarehouseItems().then((response) => {

            if (!response.success) {
                setMessageIndicatorInfo({
                    state: "error",
                    message: response.message
                })

                return
            }

            let serverItems = response.data as CafWarehouseItem[];

            let menuItemsInfo: MenuItemInfo[] = serverItems.map((item) => {
                return {
                    ...item,
                    imageUrl: appendImageOrigin(item.imageUrl),
                    selected: false
                }
            })

            setBreakfastItemsInfo(structuredClone(menuItemsInfo));
            setLunchItemsInfo(structuredClone(menuItemsInfo));
            setDinnerItemsInfo(structuredClone(menuItemsInfo));


        }).catch((e) => {
            let err = e as Error
            setMessageIndicatorInfo({
                state: "error",
                message: err.message
            });
            return
        })
    }, [])

    return (
        <div>
            <div className="w-full sm:px-0 mt-5">
                <Tab.Group>
                    <Tab.List className="flex space-x-1 rounded-xl p-1 bg-gray-800">
                        <Tab
                            className={({selected}) => resolveClassNames('w-full rounded-lg py-2.5 text-sm font-medium leading-5', selected ? 'bg-gray-700' : 'text-blue-100 hover:bg-white/[0.12] hover:text-white')}>
                            Breakfast
                        </Tab>
                        <Tab
                            className={({selected}) => resolveClassNames('w-full rounded-lg py-2.5 text-sm font-medium leading-5', selected ? 'bg-gray-700' : 'text-blue-100 hover:bg-white/[0.12] hover:text-white')}>
                            Lunch
                        </Tab>
                        <Tab
                            className={({selected}) => resolveClassNames('w-full rounded-lg py-2.5 text-sm font-medium leading-5', selected ? 'bg-gray-700' : 'text-blue-100 hover:bg-white/[0.12] hover:text-white')}>
                            Dinner
                        </Tab>
                    </Tab.List>
                    <Tab.Panels className="mt-2">

                        <Tab.Panel className={resolveClassNames('rounded-xl p-3')}>
                            <MenuPeriodBox
                                period={"Breakfast"}
                                startTime={breakfastStartTime}
                                endTime={breakfastEndTime}
                                periodItemsInformation={breakfastItemsInfo}
                                updatePeriodItemsInformation={updateBreakfastItemsInformation}
                                isReadonly={isReadonly}
                            />
                        </Tab.Panel>
                        <Tab.Panel className={resolveClassNames('rounded-xl p-3')}>
                            <MenuPeriodBox
                                period={"Lunch"}
                                startTime={lunchStartTime}
                                endTime={lunchEndTime}
                                periodItemsInformation={lunchItemsInfo}
                                updatePeriodItemsInformation={updateLunchItemsInformation}
                                isReadonly={isReadonly}
                            />
                        </Tab.Panel>
                        <Tab.Panel className={resolveClassNames('rounded-xl p-3')}>
                            <MenuPeriodBox
                                period={"Dinner"}
                                startTime={dinnerStartTime}
                                endTime={dinnerEndTime}
                                periodItemsInformation={dinnerItemsInfo}
                                updatePeriodItemsInformation={updateDinnerInformation}
                                isReadonly={isReadonly}
                            />
                        </Tab.Panel>

                    </Tab.Panels>
                </Tab.Group>
            </div>
            <div className={"mt-5 rounded"}>
                <MessageIndicator
                    state={messageIndicatorInfo.state}
                    message={messageIndicatorInfo.message}
                />
            </div>
            <div className="mt-6 flex items-center justify-end gap-x-6">
                <button
                    onClick={onMenuSave}
                    disabled={!submitButtonIsActive}
                    className={resolveClassNames(
                        "rounded-md  px-3 py-2 text-sm font-semibold  shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600",
                        submitButtonIsActive ? "bg-indigo-600 hover:bg-indigo-500 text-white" : "bg-gray-600 hover:bg-gray-600"
                    )}
                >
                    Create Menu
                </button>
            </div>
        </div>
    )
}


function NoItemInSectionMessage() {
    return (
        <div className={"p-5"}>There are no items in this section</div>
    )
}