import axios from "axios";
import {sessionManager} from "../SessionManager";
import {APPLICATION_METHODS, CAFETERIA_METHODS, MODEL_NAMES, SESSION_METHODS, USER_METHODS} from "../MethodNames";
import {CafMenuCreationInfo} from "../Pages/Management/CreateCafeteriaMenuPage";

const callMethodEndpoint: string = "https://cafeteria-api-4rfh.onrender.com/call-method";
const createSessionEndpoint: string = "https://cafeteria-api-4rfh.onrender.com/create-session"

export function appendImageOrigin(str: string) {
    return new URL(str, "https://cafeteria-api-4rfh.onrender.com/localhost:8080").toString()
}


interface APIResponse {
    responseCode: number,
    success: boolean,
    message: string,
    data: any
}

export type APIHelperResponse = Pick<APIResponse, "success" | "message" | "data">;


export async function createSession(): Promise<APIHelperResponse> {
    let axiosResponse = await axios.post(createSessionEndpoint);

    return {
        success: axiosResponse.data.success,
        message: axiosResponse.data.message,
        data: axiosResponse.data.data
    }
}

export async function authenticateUser(email: string, password: string, staySignedIn: boolean): Promise<APIHelperResponse> {
    let sessionSummary = sessionManager.getSessionSummary();

    let axiosResponse = await axios.post(callMethodEndpoint, {
        model: MODEL_NAMES.USER,
        method: USER_METHODS.SIGN_IN,
        args: {
            email: email,
            password: password,
            staySignedIn: staySignedIn
        },
        sessionId: sessionSummary.sessionId
    });

    return {
        success: axiosResponse.data.success,
        message: axiosResponse.data.message,
        data: axiosResponse.data.data
    }
}

export async function validateSession(sessionId: string): Promise<APIHelperResponse> {

    let axiosResponse = await axios.post(callMethodEndpoint, {
        model: MODEL_NAMES.SESSION,
        method: SESSION_METHODS.VALIDATE_SESSION,
        args: {},
        sessionId: sessionId
    });

    return {
        success: axiosResponse.data.success,
        message: axiosResponse.data.message,
        data: axiosResponse.data.data
    }
}

