export async function createUser(
    firstname: string,
    lastname: string,
    email: string,
    password: string,
    passwordConfirmation: string,
    groups: string[],
    avatar: string,
): Promise<APIHelperResponse> {
    let sessionSummary = sessionManager.getSessionSummary();

    let trimmedAvatar = avatar.replace(/^data:image\/\w+;base64,/, '');

    let axiosResponse = await axios.post(callMethodEndpoint, {
        model: MODEL_NAMES.USER,
        method: USER_METHODS.CREATE_USER,
        args: {
            firstname: firstname,
            lastname: lastname,
            email: email,
            password: password,
            passwordConfirmation: passwordConfirmation,
            groups: groups,
            avatar: trimmedAvatar,
        },
        sessionId: sessionSummary.sessionId
    });

    return {
        success: axiosResponse.data.success,
        message: axiosResponse.data.message,
        data: axiosResponse.data.data
    }
}


